<template>
	<div class="main_rig">
		<h4>我的订单</h4>
		<div class="fengexian"></div>
		<div class="mainp_rig_con">
			<div class="main_tig_nav">
				<span :class="{sp_type:true,sp_avtive:inNums==index?true:false}" v-for="(item,index) in dataType" :key="index" @click="dataTypeClick(index)">{{item.name}}</span>
			</div>
			<div class="main_table">
				<ul class="main_table_th clearfix">
					<li>商品图片</li>
					<li>商品信息</li>
					<li>单价（元）</li>
					<li>数量</li>
					<li>金额</li>
					<li>操作</li>
				</ul>
				<div class="main_tables" v-for="(item,indexs) in tableData" :key="indexs">
					<div class="main_table_title">订单号：{{item.orderCode}}	<span>{{item.orderStateName}}</span><b>¥{{item.totalPrice}}</b></div>
					<div class="main_table_cons clearfix">
						<div class="main_table_cons_con">
							<ul class="main_table_td clearfix" v-for="itemk in item.orderProducts" :key="itemk.id">
								<li>
									<img width="120" height="120" @click="orderDels(item.orderId)" v-lazy="apiUrl+itemk.proCover" alt="封面" class="imgs" />
								</li>
								<li>
									<a href="javascript:;" @click="orderDels(item.orderId)">{{itemk.proName}}</a>
								</li>
								<li>
									<a href="javascript:;">{{itemk.proPrice}}</a>
								</li>
								<li>{{itemk.proNum}}</li>
							</ul>
						</div>
						<ul class="main_table_tds">
							<li>
								<div class="pingjia">
									<div v-for="itemk in item.orderProducts" :key="itemk.id">
										<a href="javascript:;" v-if="item.orderState==4&&itemk.isReview==0?true:false" @click="addRe(itemk.thisId,item.orderCode)">去评价</a>
										<span v-if="item.orderState!=4||(item.orderState==4 &&itemk.isReview!=0)?true:false">¥{{itemk.proTotalPrice}}</span>
									</div>
								</div>
							</li>
							<li>
								<button type="button" class="td_close" v-if="item.orderState==1" @click="orderDelke(item.orderId)">删除</button>
								<button type="button" class="td_pay" v-if="item.orderState==1">去付款</button>
								<button type="button" class="td_pay" v-if="item.orderState==3">查看物流</button>
								<button type="button" class="td_pay" v-if="item.orderState==3" @click="queren(item.orderId)">确认收货</button>
								<button type="button" class="td_pay" @click="orderDels(item.orderId)">查看详情</button>
							</li>
						</ul>
					</div>
				</div>
			</div>
			<div class="pages" v-if="total>5?true:false">
				<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="currentPage" background :page-size="5" layout="prev, pager, next, jumper" :total="total">
				</el-pagination>
			</div>
		</div>
		<el-dialog title="提示" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
			<span>您确认收货吗!</span>
			<span slot="footer" class="dialog-footer">
			    <el-button @click="dialogVisible = false">取 消</el-button>
			    <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
			  </span>
		</el-dialog>
		<div class="tc_model" v-if="centerDialogLogin"></div>
		<div class="receipt" v-if="centerDialogLogin">
			<div class="receipt_title">
				<h3>物流信息</h3>
				<a href="javascript:;" class="receipt_close" @click="centerDialogLogin=false">
					<i class="el-icon-close"></i>
				</a>
			</div>
			<div class="wl_xi">
				<p><span>物流公司：</span>{{company}}</p>
				<p><span>物流单号：</span>{{companyNo}}</p>
			</div>
			<div class="receipt_con">
				<el-steps direction="vertical" :active="1" space="80px">
					<!--<el-step :title="item.datetime" :description="item.remark" v-for="(item,index) in wu_list" :key="index"></el-step>-->
				</el-steps>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'Order',
		data() {
			return {
				tableData: [],
				dataType: [{
						name: '全部',
						value:0
					},
					{
						name: '待付款',
						value:1
					},
					{
						name: '待发货',
						value:2
					},
					{
						name: '待收货',
						value:3
					},
					{
						name: '已完成',
						value:4
					},
					{
						name: '已取消',
						value:5
					},
					{
						name: '待评价',
						value:6
					},
					{
						name: '已评价',
						value:7
					},
				],
				total: 11,
				currentPage: 1,
				dialogVisible: false,
				centerDialogLogin: false,
				inNums: 0,
				prices:0,
				moneys:[],
				indexs:0,
				userInfo:'',
				pagesk:0,
			}
		},
		mounted(){
			let userInfo = sessionStorage.getItem('userInfo')
			this.userInfo = JSON.parse(userInfo)
			this.count();
			this.info()
		},
		methods: {
			info(){
				const _this = this
				let data = {
					SltType:_this.inNums,
					UserId:_this.userInfo.id,
					page:_this.pagesk,
					limit:5,
				}
				_this.ajax.orders(data).then(res=>{
					const rs = res.data
					if(rs.code == 200){
						_this.total=rs.data.totalCount
						_this.tableData = rs.data.pageDatas
					}else{
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error){
					console.log(error)
				})
			},
			orderDelke(val){//订单删除
				const _this = this
				let data = {
					'':val
				}
				_this.ajax.cancelorder(data).then(res=>{
					const rs = res.data
					if(rs.code == 200){
						_this.showMsg('删除成功');
					}else{
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error){
					console.log(error)
				})
			},
			queren(val){//确认收货
				const _this = this
				_this.dialogVisible = true
				let data = {
					'':val
				}
				_this.ajax.confirmreceipt(data).then(res=>{
					const rs = res.data
					if(rs.code == 200){
						_this.showMsg('收货成功');
					}else{
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error){
					console.log(error)
				})
			},
			addRe(val,sn){//去评价
				const _this = this
				this.$router.push({
					path: '/appraise',
					query:{
						addId:val,
						codes:sn
					}
				})
			},
			orderDels(val){//跳转详情
				const _this = this
				this.$router.push({
					path: '/orderDetails',
					query:{
						orderId:val,
					}
				})
			},
			handleClose(done) {
				this.$confirm('确认关闭？')
					.then(_ => {
						done();
					})
					.catch(_ => {});
			},
			handleSizeChange(val) {
				//console.log(`每页 ${val} 条`);
			},
			handleCurrentChange(val) {
				this.pagesk = val
				this.info()
			},
			dataTypeClick(val) { //点击类型切换
				this.inNums = val
			},
			count(){
				let total=0//临时总价
				const _this = this 
				_this.tableData.forEach(function(val, index) {
					val.order_con.forEach(function(vals) {
						total = vals.num * vals.price; //累计总价
						//_this.moneys.push(total)
						vals.money = total
					});
				});
				let sd = 0;
				for(let i=0;i<_this.tableData.length;i++){
					for(let s=0;s<_this.tableData[i].order_con.length;s++){
							sd += _this.tableData[i].order_con[s].money
					}
					//console.log(sd)
					_this.moneys.push(sd)
					sd = 0
				}
				
			},
		}
	}
</script>

<style scoped="">
	.main_rig {
		width: 934px;
		margin-left: 22px;
		float: left;
		padding-top: 38px;
		padding-bottom: 135px;
		font-size: 18px;
		color: #000;
	}
	
	.main_rig h4 {
		font-size: 18px;
		color: #000;
	}
	
	.main_tig_nav {
		display: flex;
		margin-bottom: 30px;
	}
	
	.main_tig_nav .sp_type {
		width: 100px;
		height: 40px;
		background: #F6F6F6;
		border-radius: 2px;
		font-size: 14px;
		text-align: center;
		line-height: 40px;
		margin-right: 26px;
		cursor: pointer;
	}
	
	.main_tig_nav .sp_avtive {
		color: #fff;
		background: #E62129;
	}
	
	.main_table {
		width: 100%;
	}
	.main_tables{
		box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.1);
		margin-bottom: 18px;
	}
	.main_table .main_table_th {
		height: 48px;
		color: #A8A8A8;
		line-height: 48px;
		font-size: 14px;
		text-align: center;
	}
	.main_table_title{
		width:933px;
		height:48px;
		background:#222831;
		color: #fff;
		padding: 0 30px 0 40px; 
		line-height: 48px;
		font-size: 14px;
	}
	.main_table_title span{
		float: right;
	}
	.main_table_title b{
		float: right;
		color: #E83239;
		width: 106px;
		margin-right: 98px;
		text-align: center;
		font-size: 16px;
		font-weight: normal;
	}
	.main_table_cons{
		display: flex;
		justify-content:stretch;
	}
	.main_table_cons .main_table_cons_con{
		width: 657px;
	}
	.main_table_cons .main_table_tds{
		width: 270px;
	}
	.main_table_cons .main_table_tds{
		height: auto;
	}
	.main_table .main_table_td {
		height: 121px;
		display: flex;
		align-items: center;
		border-bottom: 1px solid #e2e2e2;
	}
	.main_table .main_table_td:last-child{
		border-bottom: none;
	}
	.main_table .main_table_td:last-child {
		margin-bottom: 0;
	}
	
	.main_table .main_table_th li,
	.main_table .main_table_td li {
		float: left;
		width: 106px;
		text-align: center;
		position: relative;
		font-size: 14px;
	}
	.main_table .main_table_td li .imgs{
		width: 100px;
		height: 100px;
		display: block;
		margin: 5px 0 0 10px;
	}
	.main_table_tds li{
		width: 106px;
		border-left: 1px solid #e2e2e2;
		height: 100%;
		float: left;
		display: flex;
		justify-content: center;
		align-items:center;
		color: #E83239;
		
	}
	.main_table_tds li .pingjia{
		height: 100%;
		width: 100%;
		align-items:center;
		justify-content: space-around;
		display: flex;
		flex-direction:column;
	}
	.main_table_tds li .pingjia a{
		color: #fff;
		width: 80px;
		height: 28px;
		background: #E62129;
		border-radius: 2px;
		cursor: pointer;
		display: block;
		font-size: 12px;
		text-align: center;
		line-height: 28px;
		order:2;
	}
	.main_table .main_table_td li a {
		display: block;
		width: 100%;
		height: 100%;
		color: #585858;
		font-size: 14px;
	}
	
	.main_table .main_table_th li:nth-child(1),
	.main_table .main_table_td li:nth-child(1) {
		width: 166px;
		font-size: 14px;
	}
	.main_table .main_table_td li:nth-child(1) {
		cursor: pointer;
	}
	.main_table .main_table_th li:nth-child(2),
	.main_table .main_table_td li:nth-child(2) {
		width: 274px;
	}
	
	.main_table .main_table_td li:nth-child(2) {
		text-align: left;
	}
	.main_table .main_table_th li:nth-child(6){
		width: 164px;
	}
	.main_table .main_table_td li:nth-child(1) {
		line-height: normal;
	}
	
	.main_table .main_table_td li:nth-child(2) {
		color: #585858;
		font-size: 14px;
		line-height: normal;
	}
	.main_table .main_table_tds li:nth-child(2) {
		width: 164px;
		flex-direction: column;
	}

	
	.main_table_tds li:nth-child(2) .td_close {
		color: #E62129;
		width: 54px;
		height: 28px;
		background: #FFDEDF;
		border: 1px solid #E62129;
		border-radius: 2px;
		cursor: pointer;
		margin-bottom: 10px;
	}
	
	.main_table_tds li:nth-child(2) .td_pay {
		color: #fff;
		width: 80px;
		height: 28px;
		background: #E62129;
		border-radius: 2px;
		cursor: pointer;
	}
	
	.pages {
		padding: 80px 0 172px;
	}
	
	.mainp_rig_con .el-pagination {
		text-align: center;
	}
	
	.mainp_rig_con .el-pagination.is-background .btn-next,
	.mainp_rig_con .el-pagination.is-background .btn-prev,
	.mainp_rig_con .el-pagination.is-background .el-pager li {
		border: 1px solid #B1B1B1;
		background: transparent;
	}
	
	.mainp_rig_con .el-pagination.is-background .el-pager li:not(.disabled).active {
		border: 1px solid transparent;
		background: #E62129;
	}
	
	.receipt {
		width: 490px;
		position: fixed;
		left: 50%;
		top: 10%;
		border-radius: 5px;
		background: #fff;
		margin-left: -245px;
		z-index: 9999;
		padding: 0 54px 60px;
	}
	
	.receipt .receipt_title {
		text-align: center;
		position: relative;
		padding: 45px 0 20px;
		margin-bottom: 40px;
		border-bottom: 1px solid #f1f1f1;
	}
	
	.receipt .receipt_title h3 {
		font-weight: normal;
		font-size: 16px;
		line-height: 20px;
		color: #E6222A;
	}
	
	.receipt .receipt_title span {
		position: absolute;
		top: 10px;
		height: 1px;
		background: #707070;
		width: 95px;
	}
	
	.receipt_close {
		position: absolute;
		right: 0;
		top: 52px;
		font-size: 18px;
		font-weight: bold;
		margin-left: -7px;
		color: #000;
	}
	
	.wl_xi p span {
		width: 90px;
	}
	
	.receipt_con {
		max-height: 600px;
		min-height: 300px;
		overflow-y: auto;
		padding-left: 60px;
		/*position: relative;
		right: -20px;*/
	}
</style>


