<template>
	<div class="main">
		<div class="width12 clearfix">
			<div class="fengexian"></div>
			<h3>选择支付方式</h3>
			<div class="pay_ul clearfix">
				<div class="ul_item" v-for="(item,index) in payType" :key="index" @click="acvites(item.id)">
					<img :src="item.url" :alt="item.name" class="item_logo" />
					<span>{{item.name}}</span>
					<img :src="inNum==item.id?imgsUrl:imgsUrl1" width="25" height="25" alt="勾勾" class="item_img" />
				</div>
			</div>
			<div class="pay_bot">
				<h3>使用<font>{{payTypeName}}</font>进行支付</h3>
				<div class="bot_wen">
					<p><span>应付金额：</span><b>¥2858</b></p>
				</div>
			</div>
			<button type="button" class="btn_sumd">进入支付页面</button>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'Verify',
		data() {
			return {
				imgsUrl: require('../../assets/images/yxz.png'),
				imgsUrl1: require('../../assets/images/wxz.png'),
				payType: [
					{
						id: 1,
						url: require('../../assets/images/ali.png'),
						name: '支付宝',
					},
					{
						id: 2,
						url: require('../../assets/images/wx.png'),
						name: '微信支付',
					},
				],
				payTypeName:'',
				inNum: 1,
			}
		},
		watch: {
			$route(to, from) {
				if(sessionStorage.getItem('userInfo') == undefined && (sessionStorage.getItem('userInfo') == null || sessionStorage.getItem('userInfo') == '')) {
					this.$router.push({
						path: '/',
					})
					this.$root.$emit("login", 1)
				}
			}
		},
		mounted() {
			if(sessionStorage.getItem('userInfo') == undefined && (sessionStorage.getItem('userInfo') == null || sessionStorage.getItem('userInfo') == '')) {
				this.$router.push({
					path: '/',
				})
				this.$root.$emit("login", 1)
			}
		},
		methods: {
			acvites(val) {
				const _this = this
				_this.inNum = val
                for(var i=0;i<_this.payType.length;i++){
                	if(_this.payType[i].id == val){
						_this.payTypeName = _this.payType[i].name
					}
                }
			},
			
		},
	}
</script>

<style>
	.main h3{
		font-size: 18px;
		color: #000;
		margin-bottom: 42px;
	}
	
	.pay_ul .ul_item {
		float: left;
		width: 372px;
		height: 106px;
		background: rgba(255, 255, 255, 1);
		box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.05);
		position: relative;
		padding: 27px 32px;
		cursor: pointer;
		margin-right: 42px;
	}
	.pay_ul .ul_item span{
		line-height: 52px;
		color: #585858;
		font-size: 18px;
	}
	.pay_ul .ul_item .item_logo{
		float: left;
		width: 52px;
		height: 52px;	
		margin-right: 27px;	
	}
	.pay_ul .ul_item .item_img {
		position: absolute;
		right: 19px;
		top: 50%;
		margin-top: -12.5px;
		margin-right: 42px;
	}
	.pay_bot{
		clear: both;
	}
	.pay_bot h3{
		font-weight: normal;
		font-size: 16px;
		padding-top: 9px;
		margin-top: 54px; 
		margin-bottom: 32px;
		width: 100%;
	}
	.pay_bot h3 font{
		color: #1296DB;
	}
	.pay_bot .bot_wen{
		height: 83px;
		line-height: 83px;
		text-align: right;
	}
	.pay_bot .bot_wen p{
		display: flex;
		justify-content: flex-end;
	}
	.pay_bot .bot_wen p span{
		width: 70px;
		margin-right: 40px;
		display: inline-block;
		color: #585858;
	}
	.pay_bot .bot_wen p b{
		width: 83px;
		display: inline-block;
		color: #E83239;
		text-align: left;
	}
	.btn_sumd{
		width:124px;
		height:43px;
		background:#E62129;
		color: #fff;
		margin: 26px 0 57px;
		float: right;
	}
</style>


