<template>
	<div id="mains">
		<Banner />
		<div class="main width12">
			<ul class="main_naws clearfix">
				<li class="main_naws_left"><img src="../../assets/images/toutiao.png" />
					<p>知大事小事，看三品头条！</p>
				</li>
				<li class="main_naws_con"><img v-lazy="apiUrl+toutiao.banner" /></li>
				<li class="main_naws_ri">
					<a href="javascript:;" @click="seeUrl(toutiao.id)" class="seeMore">查看详情>></a>
					<p class="naws_ri_con1">{{toutiao.newsTitle}}</p>
					<p class="naws_ri_con2">{{toutiao.digest}}</p>
				</li>
			</ul>
			<div class="main_items">
				<div class="main_items_top">
					<img src="../../assets/images/title_01.png" alt="最新推荐" class="items_img" />
					<span class="items_tits">最新推荐，优惠享不停！</span>
					<a href="javascript:;" class="seeMore" @click="chakande(2)">查看更多>></a>
				</div>
				<div class="main_items_con clearfix">
					<div class="main_items_con_item" v-for="item in nawsList" @click="commDeat(item.id)">
						<div class="con_item_top clearfix">
							<span v-for="itemk in item.proImgk" class="sp_img"><img v-lazy="apiUrl+itemk"/></span>
						</div>
						<div class="con_item_wen">{{item.proName}}</div>
						<div class="con_item_bot">
							<span class="bot_sp1">￥<label>{{item.minPrice}}</label></span>
							<span class="bot_sp2">已销售{{item.sellNum}}</span>
						</div>
					</div>
				</div>
			</div>
			<a :href="adConten.linkUrl" target="_blank" class="main_ad"><img v-lazy="apiUrl+adConten.bannerUrl" :alt="adConten.title" /></a>
			<div class="main_items">
				<div class="main_items_top">
					<img src="../../assets/images/title_02.png" alt="热销商品" class="items_img" />
					<span class="items_tits">更多热销商品，供您选择！</span>
					<a href="javascript:;" class="seeMore" @click="chakande(3)">查看更多>></a>
				</div>
				<div class="main_items_con clearfix">
					<div class="main_items_con_item main_items_con_item1" v-for="item in storeList" @click="commDeat(item.id)">
						<div class="con_item_top1 clearfix"><img v-lazy="apiUrl+item.coverUrl" /></div>
						<div class="con_item_wen">{{item.proName}}</div>
						<div class="con_item_bot">
							<span class="bot_sp1">￥<label>{{item.minPrice}}</label></span>
							<span class="bot_sp2">已销售{{item.sellNum}}</span>
						</div>
					</div>
				</div>
			</div>
			<div class="main_items">
				<div class="main_items_top">
					<img src="../../assets/images/title_03.png" alt="前沿资讯" class="items_img" />
					<span class="items_tits">最新热门咨询，让您走在资讯最前沿！</span>
				</div>
				<div class="main_items_con clearfix">
					<div class="main_items_left">
						<el-carousel trigger="click" height="367px" arrow="always" :autoplay="false">
							<el-carousel-item v-for="(item,index) in opensk" :key="index" >
								<h3>{{(item.createDate!=null || item.createDate!='')&&item.createDate!=undefined?item.createDate.substring(5,10):''}}</h3>
								<p class="main_items_left_wen_tit">{{item.newsTitle}}</p>
								<p class="main_items_left_wen_con">{{item.digest}}</p>
								<div class="main_items_left_pic" @click="seeUrl(item.id)"><img v-lazy="apiUrl+item.banner" /></div>
							</el-carousel-item>
						</el-carousel>
					</div>
					<div class="main_items_ri">
						<div class="main_items_ri_naws">
							<div class="main_items_ri_naws_item" v-for="item in nawsList1">
								<p class="title">{{item.newsTitle}}<span class="origins"></span></p>
								<p class="cons">{{item.digest}}</p>
								<time>{{(item.createDate!=null || item.createDate!='')&&item.createDate!=undefined?item.createDate.substring(5,10):''}}</time>
								<a href="javascript:;" class="pic_btn" @click="seeUrl(item.id)">
									<img src="../../assets/images/right_02.png" />
								</a>
							</div>
						</div>
					</div>
				</div>
			</div>
			<div class="main_bottom">
				<p>没有更多了
					<span></span>
					<span></span>
				</p>
			</div>
		</div>
	</div>
</template>

<script>
	import Banner from '@/components/banner.vue';
	export default {
		name: 'home',
		data() {
			return {
				toutiao: '',
				adConten: '',
				nawsList: [],//最新推荐
				storeList: [],//热销商品
				opensk: [{
						title: 'iPhone X、Mate10、三星S9 对比评测5件商品iPhone X',
						name: '前言：一款手机你最看重它什么？你可能的回答是拍照美不美、吃鸡卡不卡、玩法多不多，影音娱乐给力不给力，界面功能设计是否人性化，最基本的是它能打电话，当然这是废话，打电话已经是基本功能了。所以智能...',
						url: ''
					},
				],
				nawsList1: [{
						title: 'iPhone X、Mate10、三星S9 对比评测',
						name: '今年三月，Samsung 三星发布新一代旗舰手机—Galaxy S9/S9+安卓智能手机以来，来个各种权威机构的对性能数据的检测评价一直都比较高...',
					},
				]
			}
		},
		components: {
			Banner,
		},
		mounted() {
			this.newsInfo()
			this.recoInfo()
			this.adInfo()
			this.probyInfo()
			this.topInfo()
		},
		methods: {
			chakande(val){
				const _this = this
				_this.$router.push({
					path: '/classification/',
					query: {
						id: val
					}
				})
			},
			seeUrl(val){//新闻跳转详情
				const _this = this
				_this.$router.push({
					path: '/news',
					query: {
						id: val
					}
				})
				let data = {
					'':val
				}
				_this.ajax.addnewslook(data).then(res => {
					const rs = res.data
					if(rs.code == 200) {
						
					} else {
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error)
				})
			},
			newsInfo() { //头条新闻
				const _this = this
				let data = {}
				_this.ajax.getheadline(data).then(res => {
					const rs = res.data
					if(rs.code == 200) {
						_this.toutiao = rs.data
					} else {
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error)
				})
			},
			adInfo() { //首页广告位1
				const _this = this
				let data = {}
				_this.ajax.homemiddleadvert(data).then(res => {
					const rs = res.data
					if(rs.code == 200) {
						_this.adConten = rs.data[0]
						_this.adConten.linkUrl = 'http://'+_this.adConten.linkUrl
					} else {
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error)
				})
			},
			recoInfo() { //首页最新推荐
				const _this = this
				let data = {
					'': 4
				}
				_this.ajax.gethomeisrecommend(data).then(res => {
					const rs = res.data
					if(rs.code == 200) {
						_this.nawsList = rs.data
						rs.data.forEach(function(val, i) {
							let imgs = rs.data[i].proImgs.split(",")
							let imgk = []
							imgs.forEach(function(vals, k) {
								imgk.push(vals)
							});
							imgk.unshift(rs.data[i].coverUrl)
							_this.nawsList[i].proImgk = imgk
						});
					} else {
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error)
				})
			},
			probyInfo() { //首页热销商品
				const _this = this
				let data = {
					'': 10
				}
				_this.ajax.getprobyhomehot(data).then(res => {
					const rs = res.data
					if(rs.code == 200) {
						_this.storeList = rs.data
					} else {
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error)
				})
			},
			topInfo() { //首页前沿资讯
				const _this = this
				let data = {}
				_this.ajax.topinformation(data).then(res => {
					const rs = res.data
					if(rs.code == 200) {
						_this.opensk = rs.data.left
						_this.nawsList1 = rs.data.right
					} else {
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error)
				})
			},
			//点击跳转商品详情
			commDeat(sid) {
				const _this = this
				_this.$router.push({
					path: '/commDetails/' + sid,
				})
			},
		},

	}
</script>

