<template>
	<div class="main">
		<div class="width12">
			<div class="fengexian"></div>
			<h3 class="titles">购物车</h3>
			<div class="main_table">
				<ul class="main_table_th clearfix">
					<li>
						<span class="checks">
							<img :src="isCheckedAll?imgsUrl1:imgsUrl" />
							<input type="checkbox"
								:checked="tableData.length===fruitIds.length && fruitIds.length"
								 @click="checkedAll" id="check01" />
						</span>
						<label for="check01">全选</label>
					</li>
					<li>商品信息</li>
					<li>单价（元）</li>
					<li>数量</li>
					<li>金额</li>
					<li>操作</li>
				</ul>
				<ul class="main_table_td clearfix" v-for="item in tableData" :key="item.id">
					<li>
						<span class="checks">
							<img :src="fruitIds.indexOf(item.id)>=0?imgsUrl1:imgsUrl" />
							<input type="checkbox" checked="" name="" :checked="fruitIds.indexOf(item.id)>=0"
								@click="checkedOne(item.id)">
						</span>
						<img width="120" height="120" v-lazy="apiUrl+item.proUrl" alt="加油" class="imgs" />
					</li>
					<li>{{item.proName+' '+item.priceName}}</li>
					<li>{{item.price}}</li>
					<li>
						<a href="javascript:;" :class="{num_minus:true,a_actives:fruitIds.indexOf(item.id)>=0?false:true}" :disabled="fruitIds.indexOf(item.id)>=0?false:true" @click="numMinus(item.id)"><i class="el-icon-minus"></i></a>
						<p class="nums">{{item.num}}</p>
						<a href="javascript:;" :class="{num_plus:true,a_actives:fruitIds.indexOf(item.id)>=0?false:true}" :disabled="fruitIds.indexOf(item.id)>=0?false:true" @click="numPlus(item.id)"><i class="el-icon-plus"></i></a>
					</li>
					<li>{{item.price*item.num}}</li>
					<li><i class="el-icon-delete" @click="closeComm(item.id)"></i></li>
				</ul>
			</div>
			<div class="settlement">
				<li>
					<p>
						<!--<label>实付金额满<font>88</font>元包邮</label>-->总价<span>¥{{totalPrice}}</span></p>
					<p><b>已选商品<font>{{totalNum}}</font>件</b>
						<!--已优惠<span>¥0</span>--></p>
				</li>
				<li>
					<a href="jiavascript:;" class="btn_pay" @click="herfUrls">去结算</a>
				</li>
			</div>
			<div class="fengexian"></div>
			<h3 class="titles">大家都在买</h3>
			<div class="main_content_con clearfix">
				<div class="main_content_con_item" v-for="item in bestseller">
					<img src="../../assets/images/new.png" alt="最新" class="seal" />
					<div class="item_img"><img v-lazy="apiUrl+item.coverUrl" /></div>
					<div class="item_con">
						<h3>{{item.proName}}</h3>
						<p>{{item.proBrief}}</p>
						<div class="item_con_bot clearfix">
							<span class="bot_sp">￥<label>{{item.minPrice}}</label></span>
							<div class="rig clearfix">
								<!--<a href="javascript:;"><img src="../../assets/images/Collection.png" /></a>
								<span>|</span>-->
								<a href="javascript:;" @click="gouwuche(item.id)"><img src="../../assets/images/cart.png" /></a>
							</div>
						</div>
					</div>
				</div>
			</div>
			<el-dialog :visible.sync="dialogFormVisible" class="main_comm_con">
				<div class="item_img"><img v-lazy="apiUrl+commListMone.proUrl" /></div>
				<div class="price"><span class="sp_left">商品价格</span><b>￥{{commListMone!=''?commListMone.prices[inNumsRed].priceValue:''}}</b></div>
				<div class="colour clearfix">
					<span class="sp_left">商品颜色</span>
					<div class="colour_con">
						<a href="javascript:;" v-for="(item,index) in commListMone.prices" :key="index" :class="{colour_con_item:true,item_active:inNumsRed==index?true:false}" @click="redClick(index)">{{item.priceKey}}</a>
					</div>
				</div>
				<div class="price"><span class="sp_left">商品数量</span><input type="text" class="shuliang" v-model="inNumk" /></div>
				<div slot="footer" class="dialog-footer">
					<el-button type="danger" @click="addCart(commListMone)">加入购物车</el-button>
				</div>
			</el-dialog>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'cart',
		data() {
			return {
				imgsUrl: require('../../assets/images/wselec.png'),
				imgsUrl1: require('../../assets/images/selec.png'),
				dialogFormVisible: false,
				formLabelWidth: '120px',
				commListMone: [],
				inNumsRed: 0, //选择商品颜色
				tableData: [],
				fruitIds: [],
				// 初始化全选按钮, 默认不选
				isCheckedAll: false,
				totalPrice: 0,
				totalNum: 0,
				userInfo:'',
				bestseller:[],
				inNumk: 1, //加入购物车的商品数量
			}
		},
		watch: {
			$route(to, from) {
				if(sessionStorage.getItem('userInfo') == undefined && (sessionStorage.getItem('userInfo') == null || sessionStorage.getItem('userInfo') == '')) {
					this.$router.push({
						path: '/',
					})
					this.$root.$emit("login", 1)
				}
				this.infos()
			}
		},
		mounted() {
			if(sessionStorage.getItem('userInfo') == undefined && (sessionStorage.getItem('userInfo') == null || sessionStorage.getItem('userInfo') == '')) {
				this.$router.push({
					path: '/',
				})
				this.$root.$emit("login", 1)
			}
			let userInfo = sessionStorage.getItem('userInfo')
			this.userInfo = JSON.parse(userInfo)
			this.infos()
			this.geteveryonebuy()
		},
		methods: {
			infos(){
				const _this = this
				let data = {
					'':_this.userInfo.id
				}
				_this.ajax.getcarinfo(data).then(res=>{
					const rs = res.data
					if(rs.code == 200){
						_this.tableData = rs.data
					}else{
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error){
					console.log(error)
				})
			},
			gouwuche(sid) { //加入购物车弹窗
				const _this = this
				if(sessionStorage.getItem('userInfo') != undefined && (sessionStorage.getItem('userInfo') != null || sessionStorage.getItem('userInfo') != '')) {
					_this.dialogFormVisible = true
					let data = {
						'': sid
					}
					_this.ajax.getprostandard(data).then(res => {
						const rs = res.data;
						if(rs.code == 200) {
							_this.commListMone = rs.data
							console.log(_this.commListMone)
						} else {
							_this.showMsg(rs.msg, '2');
						}
					}).catch(function(error) {
						console.log(error);
					});
				} else {
					_this.showMsg('您还没有登录哦！快去登录吧！！！', '2');
				}
			},
			redClick(val) { //选择颜色
				this.inNumsRed = val
			},
			addCart(val) {
				const _this = this
				console.log(val)
				let data = {
					ProductId: val.proId,
					PriceId: val.prices[_this.inNumsRed].id,
					TotalPrice: val.prices[_this.inNumsRed].priceValue * _this.inNumk,
					Num: _this.inNumk,
					UserId: _this.userInfo.id
				}
				_this.ajax.addcar(data).then(res => {
					const rs = res.data;
					if(rs.code == 200) {
						_this.dialogFormVisible = false
						_this.showMsg('成功加入,请到购物车查看...');
						_this.$root.$emit("cartNum", 1)
					} else {
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error);
				});
			},
			geteveryonebuy(){//大家都在买
				const _this = this
				let data = {
					'':8
				}
				_this.ajax.geteveryonebuy(data).then(res=>{
					const rs = res.data
					if(rs.code == 200){
						_this.bestseller = rs.data
					}else{
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error){
					console.log(error)
				})
			},
			closeComm(id) { //删除商品
				const _this = this
				if(_this.fruitIds.length > 0) {
					_this.counts(id, true)
					_this.countNum(2, id, true)
				}
				_this.tableData.forEach(function(val, index) {
					if(val.id == id) {
						console.log(val)
						console.log(index)
						_this.tableData.splice(index, 1);
					}
				});
				if(_this.fruitIds.length > 0) {
					_this.fruitIds.splice(_this.fruitIds.indexOf(id), 1);
				}
				let data = {
					'':id,
				}
				_this.ajax.delcar(data).then(res=>{
					const rs = res.data
					if(rs.code == 200){
						_this.$root.$emit("cartNum",1)
						_this.showMsg('删除成功');
					}else{
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error){
					console.log(error)
				})
			},
			herfUrls() {
				const _this = this
				let srt = _this.fruitIds.join(',')
				let data = {
					"":srt
				}
				_this.ajax.getcarbyids(data).then(res=>{
					const rs = res.data
					if(rs.code == 200){
						_this.$router.push({
							name:'verify',
							params:{
								carids:srt
							}
						})
					}else{
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error){
					console.log(error)
				})
			},
			checkedOne(fruitId) { //单选
				let idIndex = this.fruitIds.indexOf(fruitId)
				if(idIndex >= 0) { //如果已经包含就去除
					this.fruitIds.splice(idIndex, 1)
					this.counts(fruitId, true)
					this.countNum(2, fruitId, true)
				} else { //如果没有包含就添加
					this.fruitIds.push(fruitId)
					this.counts(fruitId, false)
					this.countNum(2, fruitId, false)
				}
				if(this.fruitIds.length == this.tableData.length){
					this.isCheckedAll = true
				}else{
					this.isCheckedAll = false
				}
			},
			checkedAll(e) {
				//				console.log(e.target.checked)
				this.isCheckedAll = e.target.checked;
				if(this.isCheckedAll) { //全选时
					this.fruitIds = []
					this.tableData.forEach(item => {
						//						console.log(item.id)
						this.fruitIds.push(item.id)
					})
					this.count();
					this.countNum(1)
				} else {
					this.fruitIds = []
					this.totalPrice = 0
				}
			},
			numMinus(id) { //商品-减法
				const _this = this
				_this.tableData.forEach((item, i) => {
					if(item.id == id) {
						if(item.num > 1) {
							item.num--
							_this.totalPrice = _this.totalPrice - (1 * item.price)
							_this.totalNum--
							_this.upCar(id,item.num,item.price)
						}
					}
				})
			},
			numPlus(id) { //商品-加法
				const _this = this
				_this.tableData.forEach((item, i) => {
					if(item.id == id) {
						item.num++
						_this.totalPrice = _this.totalPrice + (1 * item.price)
						_this.totalNum++
						_this.upCar(id,item.num,item.price)
					}
				})
				
			},
			upCar(val,nums,prices){
				const _this = this
				let data = {
					CarId:val,
					Num:nums,
					TotalPrice:prices*nums
				}
				_this.ajax.upcarinfo(data).then(res=>{
					const rs = res.data
					if(rs.code == 200){
						
					}else{
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error){
					console.log(error)
				})
			},
			count() {
				let total = 0; //临时总价
				this.tableData.forEach(function(val, index) {
					total += val.num * val.price; //累计总价
				});
				this.totalPrice = parseFloat(total);
			},
			counts(id, flsez) {
				let total = 0; //临时总价
				if(flsez) {
					this.tableData.forEach(function(val, index) {
						if(val.id == id) {
							total += val.num * val.price; //累计总价
						}
					});
					this.totalPrice = this.totalPrice - parseFloat(total);
				} else {
					this.tableData.forEach(function(val, index) {
						if(val.id == id) {
							total += val.num * val.price; //累计总价
						}
					});
					this.totalPrice = parseFloat(this.totalPrice) + parseFloat(total);
				}
			},
			countNum(type, id, flse) { //计算商品件数
				let total = 0; //临时商品件数
				if(type == 1) {
					this.tableData.forEach(function(val, index) {
						total += val.num //累计件数
					});
					this.totalNum = total
				} else {
					if(flse) {
						this.tableData.forEach(function(val, index) {
							if(val.id == id) {
								total += val.num //累计件数
							}
						});
						this.totalNum = this.totalNum - total
					} else {
						this.tableData.forEach(function(val, index) {
							if(val.id == id) {
								total += val.num //累计件数
							}
						});
						this.totalNum = this.totalNum + parseFloat(total);
					}
				}
			},
		},
	}
</script>

