		font-weight: normal;
		float: left;
	}
	
	.main_comm_con .colour {
		padding-left: 20px;
		margin: 10px 0;
	}
	
	.main_comm_con .colour .sp_left {
		line-height: 49px;
	}
	
	.main_comm_con .colour .colour_con {
		display: flex;
		float: left;
		width: 360px;
		flex-wrap: wrap;
		align-content: space-around;
	}
	
	.main_comm_con .colour .colour_con_item {
		padding: 9px 20px;
		border: 1px solid transparent;
		background: #F6F6F6;
		color: #AFAFAF;
		font-size: 14px;
		flex-grow: 0;
		border-radius: 2px;
		margin: 5px;
	}
	
	.main_comm_con .colour .item_active {
		background: #FFEEEE;
		border: 1px solid #E62129;
		color: #E62129;
	}
	
	.main_comm_con .item_img {
		width: 120px;
		height: 120px;
		margin: 0 auto 10px;
	}
	
	.main_comm_con .shuliang {
		display: block;
		width: 90px;
		height: 30px;
		border-radius: 3px;
		background: transparent;
		float: left;
		margin-top: 26px;
		padding: 0 10px;
	}
</style>


