<template>
	<div class="main">
		<div class="width12">
			<div class="title">您的位置：首页>个人中心><span>评价</span></div>
			<div class="fengexian"></div>
			<div class="que_table clearfix">
				<h4>确认商品信息</h4>
				<el-table :data="tableData" stripe style="width: 100%">
					<el-table-column label="商品信息" min-width="49%">
						<template slot-scope="scope">
							<img v-lazy="apiUrl+scope.row.proCover" alt="" width="120" height="120" class="tab_img" />
							<p class="tab_p">{{ scope.row.proName }}</p>
						</template>
					</el-table-column>
					<el-table-column prop="proPrice" label="单价(元）" min-width="17%">
					</el-table-column>
					<el-table-column prop="proNum" label="数量" min-width="17%">
					</el-table-column>
					<el-table-column prop="proTotalPrice" label="金额" min-width="17%">
					</el-table-column>
				</el-table>
			</div>
			<div class="main_con clearfix">
				<h4>发表评论</h4>
				<div class="fabiao clearfix">
					<div class="file_wrap" v-for="(item,index) in imgList">
						<img :src="item.fileUrl" alt="评论">
						<div class="del" @click="delImg(index)"><img src="../../assets/images/jdt_044.png" alt=""></div>
					</div>
					<label class="curp" v-show="imgList.length>=6?false:true">
						<input type="file" class="hide" @change="fileChange($event)" multiple="multiple" id="uploadFile" accept="image/jpg,image/jpeg,image/png">
						<img src="../../assets/images/sc_bg.png"/>
						<input type="hidden" v-model="form.imgList" name="photo" >
					</label>
				</div>
				<div class="text_wen">
					<textarea name="" class="area_text" @input="descInput" v-model="desc" placeholder="请输入评论内容..."></textarea>
					<p>{{remnant}}/500</p>
				</div>
				<button type="button" class="btn_submit" @click="submit">确认提交</button>
			</div>
			<!--<div class="fengexian"></div>
			<div class="main_tab_pl">
				<h3>全部评论(123)</h3>
				<div class="main_tab_pl_con clearfix">
					<div class="main_tab_pl_con_item clearfix" v-for="item in 6">
						<div class="item_pic"><img src="../../assets/images/xiao.png"/></div>
						<div class="item_ri_con">
							<h4>设计的狮子</h4>
							<p>手机拿到了，很不错，比想象中的要好。华为在细节上要比苹果差一点，有些细节还是比较粗糙，比如耳机数据线的包装，比起苹果看起来就像山寨的一样。系统界面针对屏幕的优化不够，在软件使用时，圆角跟软件不匹配，但是作为买的第一个安卓手机，总体满意。</p>
							<div class="item_pic_in">
								<img src="../../assets/images/plt.png"/>
								<img src="../../assets/images/plt.png"/>
								<img src="../../assets/images/plt.png"/>
								<img src="../../assets/images/plt.png"/>
							</div>
							<div class="arrow_num clearfix">
								<time>2019-02-24</time>
								<a href="javascript:;" class="zand"><img src="../../assets/images/zan_01.png" width="17" height="17"/>54545</a>
							</div>
						</div>
					</div>
				</div>
				<div class="pages">
					<el-pagination
				     	@size-change="handleSizeChange"
				      	@current-change="handleCurrentChange"
				      	:current-page.sync="currentPage"
				      	background
				      	:page-size="100"
				      	layout="prev, pager, next, jumper"
				      	:total="1000">
				    </el-pagination>
				</div>
			</div>-->
		</div>
	</div>
</template>

<script>
	export default {
		name: 'appraise',
		data() {
			return {
				tableData: [],
				form:{
					content:'',
					title:'',
					thumbnail:'',
					nickName:''
				},
				desc:'',
				remnant:0,
				total: 11,
				currentPage: 1,
				dialogVisible: false,
				centerDialogLogin: false,
				totalPrice: 0,
				limit: 6,
				imgList: [],
				imgsList: [],
				addId:'',
				codes:'',
			}
		},
		watch: {
			$route(to, from) {
				if(sessionStorage.getItem('userInfo') == undefined && (sessionStorage.getItem('userInfo') == null || sessionStorage.getItem('userInfo') == '')) {
					this.$router.push({
						path: '/',
					})
					this.$root.$emit("login", 1)
				}
				this.addId = this.$route.query.addId
				this.codes = this.$route.query.codes
			}
		},
		created() {
			this.addId = this.$route.query.addId
			this.codes = this.$route.query.codes
		},
		mounted() {
			if(sessionStorage.getItem('userInfo') == undefined && (sessionStorage.getItem('userInfo') == null || sessionStorage.getItem('userInfo') == '')) {
				this.$router.push({
					path: '/',
				})
				this.$root.$emit("login", 1)
			}
			this.addId = this.$route.query.addId
			this.codes = this.$route.query.codes
			let userInfo = sessionStorage.getItem('userInfo')
			this.userInfo = JSON.parse(userInfo)
			this.info()
		},
		methods: {
			info(){
				const _this = this
				let data = {
					'':_this.addId,
				}
				_this.ajax.orderproductdetail(data).then(res=>{
					const rs = res.data
					if(rs.code == 200){
						_this.tableData.push(rs.data)
					}else{
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error){
					console.log(error)
				})
			},
			descInput(){
				this.remnant = 500 - this.desc.length
			},
			handleSizeChange(val) {
				console.log(`每页 ${val} 条`);
			},
			handleCurrentChange(val) {
				console.log(`当前页: ${val}`);
			},
			submit() {
				const _that = this
				const urls = _that.imgsList.join(",");
				const data = {
					OrderCode:_that.codes,
					imgIds:urls,
					ReviewContent:_that.desc,
					ProId:_that.tableData[0].proId,
					UserId:_that.userInfo.id,
					OrderProId:_that.tableData[0].thisId,
				}
				console.log(data)
				_that.ajax.addReviwe(data).then(res => {
					const rs = res.data;
					if(rs.code == '200') {
						_that.$router.push({
							path: '/order',
						})
					} else {
						_that.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {});
			},
			fileChange(el) {
				if(!el.target.files[0].size) return;
				this.fileList(el.target);
				el.target.value = ''
			},
			fileList(fileList) {
				const files = fileList.files;
				for(let i = 0; i < files.length; i++) {
					//判断是否为文件夹
					if(files[i].type != '') {
						this.fileAdd(files[i]);
					} else {
						//文件夹处理
						this.folders(fileList.items[i]);
					}
				}
			},
			//图片删除
			delImg(num) {
				const _this = this;
				console.log(num)
				_this.imgList.splice(num, 1);
			},
			//文件夹处理
			folders(files) {
				const _this = this;
				//判断是否为原生file
				if(files.kind) {
					files = files.webkitGetAsEntry();
				}
				files.createReader().readEntries(function(file) {
					for(let i = 0; i < file.length; i++) {
						if(file[i].isFile) {
							_this.foldersAdd(file[i]);
						} else {
							_this.folders(file[i]);
						}
					}
				});
			},
			foldersAdd(entry) {
				const _this = this;
				entry.file(function(file) {
					_this.fileAdd(file)
				})
			},
			fileAdd(file) {
				const _that = this;
				if(_that.limit !== undefined && _that.limit <= 0) {
					_that.showMsg('已到最大上传数量', '2');
					return;
				}
				const imgData = new FormData();
				imgData.append('file', file);
				imgData.append('buyerId', _that.userInfo.id);
				_that.ajax.updateImg(imgData).then(res => {
					const rs = res.data;
					if(rs.code == '200') {
						if(_that.limit !== undefined) {
							_that.limit--
						};
						rs.data.fileUrl = _that.apiUrl+rs.data.fileUrl
						_that.imgList.push(rs.data);
						_that.imgsList.push(rs.data.fileId)
					} else {
						_that.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error);
				});
			},
		}
	}
</script>

<style>
	.fengexian {
		margin: 0 auto;
	}
	
	.main .title {
		height: 58px;
		line-height: 58px;
		color: #AFAFAF;
	}
	
	.main .title span {
		color: #E62129;
	}
	
	.main_con h4 {
		font-size: 18px;
		color: #000;
	}
	
	.que_table {
		margin: 24px 0 38px;
	}
	
	.que_table h4 {
		color: #000;
		font-size: 18px;
		margin-bottom: 35px;
	}
	
	.el-table {
		box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.05);
	}
	
	.que_table .tab_img {
		float: left;
	}
	
	.que_table .tab_p {
		float: left;
		width: 60%;
		line-height: 20px;
		font-size: 14px;
		color: #585858;
		margin-left: 50px;
		margin-top: 43px;
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
	}
	
	.que_table .has-gutter th {
		color: #fff;
		background: #222831;
	}
	.fabiao{
		margin: 33px 0 0 10px;
	}
	.text_wen{
		position: relative;
	}
	.text_wen .area_text{
		width:100%;
		height:142px;
		background:#F8F8F8;
		border-radius:3px;
		padding: 26px 36px;
		clear: both;
		display: block;
		margin-top: 26px;
		color: #AFAFAF;
	}
	.text_wen p{
		position: absolute;
		right: 11px;
		bottom: 13px;
		font-size: 12px;
		color: #585858;
	}
	.file_wrap {
		position: relative;
		width:70px;
		height:70px;
		border:1px solid #D5D5D5;
		border-radius:3px;
		float: left;
		margin-right: 16px;
	}
	
	.file_wrap img {
		width: 100%;
		height: 100%;
		border-radius: 4px;
	}
	
	.file_wrap .del {
		position: absolute;
		top: 6px;
		right: 6px;
		width: 20px;
		height: 20px;
		cursor: pointer;
		line-height: normal
	}
	.file_wrap~.tips {
		float: left;
		display: block;
		margin-left: 8px;
		padding-top: 62px;
	}
	.curp{
		float: left;
		width:70px;
		height:70px;
		border-radius:3px;
		position: relative;
	}
	.curp input{
		opacity: 0;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
	}
	.curp img {
		width: 100%;
		height: 100%;
		border-radius: 3px;
	}
	.btn_submit{
		width:100px;
		height:38px;
		background:#E62129;
		border-radius:2px;
		color: #fff;
		float: right;
		margin: 38px 0 53px;
	}
	.main_tab_pl{
		margin-top: 20px;
	}
	.main_tab_pl h3{
		color: #000;
		font-size: 18px;
		height: 44px;
		line-height: 44px;
	}
	.main_tab_pl .main_tab_pl_con{
		
	}
	.main_tab_pl .main_tab_pl_con_item{
		margin-top: 20px;
	}
	.main_tab_pl .main_tab_pl_con_item .item_pic{
		width: 38px;
		height: 38px;
		border-radius: 100%;
		float: left;
		margin-right: 18px;
	}
	.main_tab_pl .main_tab_pl_con_item .item_pic img{
		border-radius: 100%;
	}
	.main_tab_pl .main_tab_pl_con_item .item_ri_con{
		float: left;
		width: 1140px;
		border-bottom: 1px solid #E5E5E5;
	}
	.main_tab_pl .main_tab_pl_con_item .item_ri_con h4{
		height: 38px;
		line-height: 38px;
		color: #AFAFAF;
		font-size: 14px;
	}
	.main_tab_pl .main_tab_pl_con_item .item_ri_con p{
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		font-size: 14px;
		color: #585858;
		line-height: 22px;
		margin-bottom: 10px;
	}
	.main_tab_pl .main_tab_pl_con_item .item_pic_in{
		display: flex;
		flex-wrap:wrap;
	}
	.main_tab_pl .main_tab_pl_con_item .item_pic_in img{
		width: 50px;
		height: 50px;
		margin-right: 20px;
	}
	.main_tab_pl .main_tab_pl_con_item .arrow_num{
		padding: 15px 0;
	}
	.main_tab_pl .main_tab_pl_con_item .arrow_num time{
		float: left;
		color: #AFAFAF;
	}
	.main_tab_pl .main_tab_pl_con_item .arrow_num .zand{
		float: right;
		color: #AFAFAF;
		line-height: 17px;
	}
	.main_tab_pl .main_tab_pl_con_item .arrow_num .zand img{
		float: left;
		margin-right: 9px;
	}
	.main_tab_pl .main_tab_pl_con_item .arrow_num .zan_active{
		color: #E62129;
	}
	.pages{
		padding: 80px 0;
	}
	.main_tab_pl .el-pagination{
		text-align: center;
	}
	.main_tab_pl .el-pagination.is-background .btn-next,.main_tab_pl .el-pagination.is-background .btn-prev,.main_tab_pl .el-pagination.is-background .el-pager li{
		border: 1px solid #B1B1B1;
		background:transparent;
	}
	.main_tab_pl .el-pagination.is-background .el-pager li:not(.disabled).active{
		border: 1px solid transparent;
		background: #E62129;
	}
</style>


