<template>
		<div class="main width12">
			<div class="title">您的位置：首页>三品头条><span>头条详情</span></div>
			<div class="main_top">
				<div class="pic"><!--<img v-lazy="apiUrll+contentHtml.banner"/>--><img src="../../assets/images/bann1.png"/></div>
				<h3>{{contentHtml.newsTitle}}</h3>
				<time>{{(contentHtml.createDate !=null || contentHtml.createDate!='')&&contentHtml.createDate!=undefined?contentHtml.createDate.substring(0,10):''}}</time>
			</div>
			<div v-html="contentHtml.content"></div>
			<div class="main_bottom">
				<p>没有更多了
					<span></span>
					<span></span>
				</p>
			</div>
		</div>
</template>

<script>
	export default {
		name: 'news',
		data() {
			return {
				contentHtml:'',
				id:''
			}
		},
		watch: {
			$route(to, from) {
				this.id = this.$route.query.id;
			}
		},
		created() {
			this.id = this.$route.query.id;
		},
		mounted(){
			this.id = this.$route.query.id;
			this.info()
		},
		methods: {
			info(){
				const _this = this
				let data = {
					"":_this.id
				}
				_this.ajax.newsdetail(data).then(res => {
					const rs = res.data
					if(rs.code == 200) {
						_this.contentHtml = rs.data
					} else {
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error)
				})
			}
		},

	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
	.main .title {
		height: 58px;
		line-height: 58px;
		color: #AFAFAF;
	}
	.main .title span {
		color: #E62129;
	}
	.main_top{
		margin-bottom: 37px;
	}
	.main_top img{
		display: block;
		width: 100%;
		height: 100%;
	}
	.main_top .pic{
		height: 120px;
		margin-bottom: 50px;
	}
	.main_top h3{
		height: 40px;
		font-size: 22px;
		font-weight:bold;
		color: #585858;
	}
	.main_top time{
		display: block;
		color: #AFAFAF;
		font-size: 12px;
	}
	.main .main_bottom {
		height: 110px;
		line-height: 110px;
		text-align: center;
	}
	
	.main .main_bottom p {
		font-weight: normal;
		font-size: 12px;
		color: #AFAFAF;
		position: relative;
		display: inline-block;
	}
	
	.main .main_bottom p span {
		position: absolute;
		top: 55px;
		height: 1px;
		background: #E5E5E5;
		width: 95px;
	}
	
	.main .main_bottom p span:nth-child(1) {
		left: -110px;
	}
	
	.main .main_bottom p span:nth-child(2) {
		right: -110px;
	}
</style>


