<template>
	<div id="maina">
		<div class="main width12">
			<div class="main_banner">
				<el-carousel trigger="click" height="460px">
					<el-carousel-item v-for="(item,index) in opensk" :key="index">
						<a :href="item.linkUrl" target="_blank" class="hrefs"><img v-lazy="apiUrl+item.bannerUrl" :alt="item.title" /></a>
					</el-carousel-item>
				</el-carousel>
			</div>
			<div class="main_ad clearfix">
				<a :href="item.linkUrl" target="_blank"  class="ad_img" v-for="(item,index) in adLists" :key="index"><img v-lazy="apiUrl+item.bannerUrl" :alt="item.title" /></a>
			</div>
			<div class="fengexian"></div>
			<div class="main_content">
				<div class="main_content_title clearfix">
					<div class="title_sp">
						<span :class="{sp_avtive:inNums==index?true:false}" v-for="(item,index) in spNav" :key="index" @click="spTitle(index)">{{item.name}}<b v-if="index==3?false:true"></b></span>
					</div>
					<el-select v-model="value4" clearable placeholder="筛选" @change="shaixuan">
						<el-option v-for="item in options" :key="item.classId" :label="item.className" :value="item.classId">
						</el-option>
					</el-select>
				</div>
				<div class="main_content_con clearfix">
					<div class="main_content_con_item" v-for="item in commList">
						<img src="../../assets/images/new.png" alt="最新" class="seal" />
						<div class="item_img" @click="commDeat(item.id)"><img v-lazy="apiUrl+item.proBannerId" /></div>
						<div class="item_con">
							<h3 @click="commDeat(item.id)">{{item.proName}}</h3>
							<p @click="commDeat(item.id)">{{item.proBrief}}</p>
							<div class="item_con_bot clearfix">
								<span class="bot_sp">￥<label>{{item.proPrice}}</label></span>
								<div class="rig clearfix">
									<!--<a href="javascript:;"><img src="../../assets/images/Collection.png" /></a>
									<span>|</span>-->
									<a href="javascript:;" @click="gouwuche(item.id)"><img src="../../assets/images/cart.png" /></a>
								</div>
							</div>
						</div>
					</div>
				</div>
				<div class="pages" v-if="total>8">
					<el-pagination :current-page="countList.num" @current-change="handleCurrentChange" background :page-size="countList.size" layout="prev, pager, next, jumper" :total="total">
					</el-pagination>
				</div>
			</div>
			<el-dialog :visible.sync="dialogFormVisible" class="main_comm_con">
				<div class="item_img"><img v-lazy="apiUrl+commListMone.proUrl" /></div>
				<div class="price"><span class="sp_left">商品价格</span><b>￥{{commListMone!=''?commListMone.prices[inNumsRed].priceValue:''}}</b></div>
				<div class="colour clearfix">
					<span class="sp_left">商品颜色</span>
					<div class="colour_con">
						<a href="javascript:;" v-for="(item,index) in commListMone.prices" :key="index" :class="{colour_con_item:true,item_active:inNumsRed==index?true:false}" @click="redClick(index)">{{item.priceKey}}</a>
					</div>
				</div>
				<div class="price"><span class="sp_left">商品数量</span><input type="text" class="shuliang" v-model="inNumk" /></div>
				<div slot="footer" class="dialog-footer">
					<el-button type="danger" @click="addCart(commListMone)">加入购物车</el-button>
				</div>
			</el-dialog>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'home',
		data() {
			return {
				countList: {
					num: 1,
					size: 8,
					counts: 3
				},
				commListMone: [],
				inNumsRed: 0, //选择商品颜色
				dialogFormVisible: false,
				formLabelWidth: '120px',
				id: '',
				keys: '',
				spNav: [{
						name: '全部商品'
					},
					{
						name: '推荐'
					},
					{
						name: '最新'
					},
					{
						name: '热销'
					},
				],
				opensk: [],
				adLists:[],
				currentPage3: 5,
				options: [],
				value4: '',
				inNums: 0,
				inNumk: 1, //加入购物车的商品数量
				total: 10,
				commList: [],
				pages: 0,
				userInfo: '',
			}
		},
		watch: {
			$route(to, from) {
				this.path = this.$route.path;
				this.id = this.$route.query.id;
				this.keys = this.$route.query.keys;
				this.info()
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.keys = this.$route.query.keys;
		},
		mounted() {
			this.id = this.$route.query.id;
			this.keys = this.$route.query.keys;
			let userInfo = sessionStorage.getItem('userInfo')
			this.userInfo = JSON.parse(userInfo)
			this.info()
			this.banners()
			this.filter()
		},
		methods: {
			shaixuan(){
				const _this = this
				_this.info()
			},
			filter(){
				const _this = this
				let data = {}
				_this.ajax.getproclass(data).then(res=>{
					const rs = res.data
					if(rs.code == 200){
						_this.options = rs.data
					}else{
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error){
					console.log(error)
				})
			},
			banners(){
				const _this = this
				let data = {}
				_this.ajax.classpageadvert(data).then(res=>{
					const rs = res.data
					if(rs.code == 200){
						_this.opensk = rs.data.bannerAdvert
						_this.opensk.forEach(function(val,i){
							_this.opensk[i].linkUrl = 'http://'+_this.opensk[i].linkUrl
						})
						_this.adLists = rs.data.middleAdvert
						_this.adLists.forEach(function(val,i){
							_this.adLists[i].linkUrl = 'http://'+_this.adLists[i].linkUrl
						})
						console.log(_this.adLists)
					}else{
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error){
					console.log(error)
				})
			},
			addCart(val) {
				const _this = this
				console.log(val)
				let data = {
					ProductId: val.proId,
					PriceId: val.prices[_this.inNumsRed].id,
					TotalPrice: val.prices[_this.inNumsRed].priceValue * _this.inNumk,
					Num: _this.inNumk,
					UserId: _this.userInfo.id
				}
				_this.ajax.addcar(data).then(res => {
					const rs = res.data;
					if(rs.code == 200) {
						_this.dialogFormVisible = false
						_this.showMsg('成功加入,请到购物车查看...');
						_this.$root.$emit("cartNum", 1)
					} else {
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error);
				});
			},
			gouwuche(sid) { //加入购物车弹窗
				const _this = this
				if(sessionStorage.getItem('userInfo') != undefined && (sessionStorage.getItem('userInfo') != null || sessionStorage.getItem('userInfo') != '')) {
					_this.dialogFormVisible = true
					let data = {
						'': sid
					}
					_this.ajax.getprostandard(data).then(res => {
						const rs = res.data;
						if(rs.code == 200) {
							_this.commListMone = rs.data
							console.log(_this.commListMone)
						} else {
							_this.showMsg(rs.msg, '2');
						}
					}).catch(function(error) {
						console.log(error);
					});
				} else {
					_this.showMsg('您还没有登录哦！快去登录吧！！！', '2');
				}
			},
			commDeat(sid) {
				const _this = this
				_this.$router.push({
					path: '/commDetails/' + sid,
				})
			},
			handleCurrentChange(val) { //点击当前页
				this.pages = val
				this.info()
			},
			spTitle(val) {
				this.inNums = val
				this.id = val
				this.info()
			},
			redClick(val) { //选择颜色
				this.inNumsRed = val
			},
			info() { //页面数据
				const _that = this
				let data = {
					Type: _that.id,
					page: _that.pages,
					limit: 8,
				}
				if(_that.value4 != ''){
					data.classId=_that.value4
				}else{
					delete data.classId
				}
				if( _that.keys != undefined){
					data.KeyWork=_that.keys
				}else{
					delete data.KeyWork
				}
				_that.ajax.getprolist(data).then(res => {
					const rs = res.data;
					if(rs.code == 200) {
						_that.total = rs.data.totalCount
						_that.commList = rs.data.pageDatas
					} else {
						_that.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error);
				});
			}
		},
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
	#maina .main img {
		width: 100%;
		height: 100%;
	}
	
	#maina .main_banner .el-carousel__item {
		padding-bottom: 46px;
	}
	#maina .main_banner .hrefs{
		display: block;
	}
	#maina .main_banner .el-carousel__indicators .el-carousel__indicator {
		width: 20px;
		height: 20px;
		padding: 0;
		margin-right: 5px;
	}
	
	#maina .main_banner .el-carousel__button {
		background: url(../../assets/images/origin_01.png);
		background-size: 100% 100%;
		width: 20px;
		height: 20px;
	}
	
	#maina .main_banner .el-carousel__indicators .is-active {
		background: url(../../assets/images/active_01.png);
	}
	
	#maina .main_ad {
		margin: 56px 0 23px;
	}
	
	#maina .main_ad .ad_img {
		float: left;
		width: 583px;
		height: 193px;
		margin-right: 34px;
	}
	
	#maina .main_ad .ad_img:nth-child(2) {
		margin-right: 0;
	}
	
	#maina .main_content {}
	
	#maina .main_content .main_content_title {
		margin-bottom: 35px;
	}
	
	#maina .main_content .main_content_title .title_sp {
		text-align: center;
		display: flex;
		height: 24px;
		line-height: 24px;
		float: left;
	}
	
	#maina .main_content .main_content_title .title_sp span {
		margin-right: 20px;
		color: #A8A8A8;
		font-size: 14px;
		cursor: pointer;
		position: relative;
	}
	
	#maina .main_content .main_content_title .title_sp span b {
		font-weight: normal;
		border-right: 1px solid #A8A8A8;
		height: 11px;
		width: 0;
		margin-left: 25px;
		display: block;
		float: right;
		margin-top: 5px;
	}
	
	#maina .main_content .main_content_title .el-select {
		width: 128px;
		height: 32px;
		float: right;
	}
	
	#maina .main_content .main_content_title .el-select .el-input__inner {
		border: none;
		background: #F8F8F8;
	}
	
	#maina .main_content .main_content_title .title_sp .sp_avtive {
		color: #000;
		font-size: 18px;
	}
	
	#maina .main_content_con .main_content_con_item {
		position: relative;
		box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.1);
		float: left;
		margin: 0 34px 35px 0;
		background: #fff;
		cursor: pointer;
	}
	
	#maina .main_content_con .main_content_con_item:nth-child(4n+4) {
		margin-right: 0;
	}
	
	#maina .main_content_con .main_content_con_item .seal {
		position: absolute;
		right: 18px;
		top: 23px;
		width: 33px;
		height: 11px;
	}
	
	#maina .main_content_con .main_content_con_item .item_img {
		width: 204px;
		height: 204px;
		margin: 34px 35px;
	}
	#maina .main_content_con .main_content_con_item .item_img img{
		width:100%;
		height: 100%;
	}
	#maina .main_content_con .main_content_con_item .item_con {
		border-top: 1px solid #D6D6D6;
		padding: 0 15px 27px;
	}
	
	#maina .main_content_con .main_content_con_item .item_con h3 {
		width: 243px;
		height: 38px;
		margin: 20px 0;
		font-size: 14px;
		color: #585858;
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
	}
	
	#maina .main_content_con .main_content_con_item .item_con p {
		overflow: hidden;
		/*超出的文本隐藏*/
		text-overflow: ellipsis;
		/*溢出用省略号显示*/
		white-space: nowrap;
		/*溢出不换行*/
		width: 100%;
		height: 16px;
		font-size: 12px;
		line-height: 16px;
		color: #AFAFAF;
		margin: 10px 0 16px;
	}
	
	#maina .main_content_con .main_content_con_item .item_con .item_con_bot .bot_sp {
		font-size: 14px;
		color: #E62129;
		float: left;
	}
	
	.main_content_con .main_content_con_item .item_con .item_con_bot .bot_sp label {
		font-size: 20px;
	}
	
	.main_content_con .main_content_con_item .item_con .item_con_bot .rig {
		float: right;
	}
	
	.main_content_con .main_content_con_item .item_con .item_con_bot .rig a {
		width: 15px;
		height: 15px;
		display: block;
		float: left;
		margin-top: 6px;
	}
	
	.main_content_con .main_content_con_item .item_con .item_con_bot .rig span {
		margin: 0 15px;
		float: left;
		font-size: 12px;
	}
	
	.pages {
		padding: 80px 0 172px;
	}
	
	.main_content .el-pagination {
		text-align: center;
		color: #A3A3A3;
	}
	
	.main_content .el-pagination.is-background .btn-next,
	.main_content .el-pagination.is-background .btn-prev,
	.main_content .el-pagination.is-background .el-pager li {
		border: 1px solid #B1B1B1;
		background: transparent;
		color: #A3A3A3;
	}
	
	.main_content .el-pagination.is-background .el-pager li:not(.disabled).active {
		border: 1px solid transparent;
		background: #E62129;
	}
	
	.main_content .el-pagination.el-pagination__jump {
		float: right;
	}
	
	.main_comm_con .el-dialog {
		width: 500px;
	}
	
	.main_comm_con .price {
		height: 80px;
		background: rgba(255, 232, 233, .5);
		line-height: 80px;
		color: #E62129;
		padding-left: 20px;
	}
	
	.main_comm_con .sp_left {
		color: #AFAFAF;
		margin-right: 22px;
		float: left;
	}
	
	.main_comm_con .price b {
		font-size: 20px;
		font-weight: normal;
		float: left;
	}
	
	.main_comm_con .colour {
		padding-left: 20px;
		margin: 10px 0;
	}
	
	.main_comm_con .colour .sp_left {
		line-height: 49px;
	}
	
	.main_comm_con .colour .colour_con {
		display: flex;
		float: left;
		width: 360px;
		flex-wrap: wrap;
		align-content: space-around;
	}
	
	.main_comm_con .colour .colour_con_item {
		padding: 9px 20px;
		border: 1px solid transparent;
		background: #F6F6F6;
		color: #AFAFAF;
		font-size: 14px;
		flex-grow: 0;
		border-radius: 2px;
		margin: 5px;
	}
	
	.main_comm_con .colour .item_active {
		background: #FFEEEE;
		border: 1px solid #E62129;
		color: #E62129;
	}
	
	.main_comm_con .item_img {
		width: 120px;
		height: 120px;
		margin: 0 auto 10px;
	}
	
	.main_comm_con .shuliang {
		display: block;
		width: 90px;
		height: 30px;
		border-radius: 3px;
		background: transparent;
		float: left;
		margin-top: 26px;
		padding: 0 10px;
	}
</style>


