<template>
	<div class="main">
		<div class="width12">
			<div class="fengexian"></div>
			<div class="top_title">
				<h3>选择收货地址</h3>
				<button type="button" class="btn_citys" @click="centerDialogLogin = true">
					<i class="el-icon-plus"></i>
					添加收货地址
				</button>
			</div>
			<div class="address_ul clearfix">
				<div class="ul_item" v-for="(item,index) in cityList" :key="index" @click="acvites(item.id)">
					<img :src="inNum==item.id?imgsUrl:imgsUrl1" width="25" height="25" alt="勾勾" class="item_img" />
					<h3>{{item.userName}}</h3>
					<p class="phone">{{item.userPhone}}</p>
					<p class="city_add">{{item.addressDetail}}</p>
				</div>
			</div>
			<div class="que_table clearfix">
				<h3>确认商品信息</h3>
				<el-table :data="tableData" stripe style="width: 100%">
					<el-table-column label="商品信息" min-width="49%">
						<template slot-scope="scope">
							<img v-lazy="apiUrl+scope.row.proUrl" alt="" width="120" height="120" class="tab_img" />
							<p class="tab_p">{{ scope.row.proName }}</p>
						</template>
					</el-table-column>
					<el-table-column prop="price" label="单价(元）" min-width="17%">
					</el-table-column>
					<el-table-column prop="num" label="数量" min-width="17%">
					</el-table-column>
					<el-table-column prop="totalPrice" label="金额" min-width="17%">
					</el-table-column>
				</el-table>
				<div class="settlement">
					<p>
						<span>商品总计：</span>
						<b>¥{{totalPrices}}</b>
					</p>
					<p>
						<span>数量：</span>
						<b>{{totalNum}}</b>
					</p>
					<p>
						<span>总价：</span>
						<b class="red">¥{{totalPrices}}</b>
					</p>
				</div>
				<button type="button" class="btn_sumei" @click="haedUrl">提交订单</button>
			</div>
		</div>
		<div class="tc_model" v-if="centerDialogLogin"></div>
		<div class="receipt" v-if="centerDialogLogin">
			<div class="receipt_title">
				<h3>添加收货地址</h3>
				<a href="javascript:;" class="receipt_close" @click="centerDialogLogin=false">
					<i class="el-icon-close"></i>
				</a>
			</div>
			<div class="receipt_con">
				<p class="con_item">
					<span class="sp_img">收货人</span>
					<input type="text" class="inp_text" v-model="forms.name" placeholder="请输入收货人姓名或称呼 如：李先生" />
				</p>
				<p class="con_item">
					<span class="sp_img">手机号</span>
					<input type="text" class="inp_text" @change="changeCount" maxlength="11" v-model="forms.phone" placeholder="请输入收货人手机号" />
				</p>
				<p class="con_item">
					<span class="sp_img">收货地址</span>
					<input type="text" class="inp_text" v-model="forms.city" placeholder="请输入收货详细地址" />
				</p>
				<p class="con_item clearfix">
					<a href="javascript:;" @click="setting1" class="bot_left">
						<span><img v-lazy="forms.defaults?imgsUrl:imgsUrl1"/></span> 设为默认地址
					</a>
				</p>
				<button type="button" @click="addCity" class="inp_but">确认</button>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'verify',
		data() {
			return {
				imgsUrl: require('../../assets/images/yxz.png'),
				imgsUrl1: require('../../assets/images/wxz.png'),
				cityList: [],
				tableData: [{
					id: 1,
					url: require('../../assets/images/qiu.png'),
					name: '瑞典FOREO斐珞尔 LUNA™露娜mini2迷你净透洁面仪 粉色',
					price: 1029,
					num: 1,
					money: 1029
				}, ],
				forms: {
					name: '',
					phone: '',
					city: '',
					defaults: false,
				},
				totalPrice: 0,
				inNum: '',
				centerDialogLogin: false,
				userInfo: {},
				carids: '',
				totalPrices: '',
				totalNum: '',
			}
		},
		watch: {
			$route(to, from) {
				if(sessionStorage.getItem('userInfo') == undefined && (sessionStorage.getItem('userInfo') == null || sessionStorage.getItem('userInfo') == '')) {
					this.$router.push({
						path: '/',
					})
					this.$root.$emit("login", 1)
				}
				this.carids = this.$route.params.carids
			}
		},
		created() {
			this.carids = this.$route.params.carids
		},
		mounted() {
			if(sessionStorage.getItem('userInfo') == undefined && (sessionStorage.getItem('userInfo') == null || sessionStorage.getItem('userInfo') == '')) {
				this.$router.push({
					path: '/',
				})
				this.$root.$emit("login", 1)
			}
			this.carids = this.$route.params.carids
			let userInfo = sessionStorage.getItem('userInfo')
			this.userInfo = JSON.parse(userInfo)
			this.count();
			this.cityInfo()
			this.infos()
		},
		methods: {
			haedUrl() {
				const _this = this
				let data = {
					CarIds: _this.carids,
					UserId:_this.userInfo.id,
					AddressId:_this.inNum
				}
				console.log(_this.carids)
				for(let i in data){
					if(data[i] == ''){
						_this.showMsg('请选择收货地址', '2');
						return
					}
				}
				_this.ajax.createorder(data).then(res => {
					const rs = res.data;
					if(rs.code == 200) {
//						_this.$router.push({
//							name: 'pay',
//						})
					} else {
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error);
				});

			},
			cityInfo() { //地址
				const _this = this
				let data = {
					'': _this.userInfo.id,
				}
				console.log(_this.userInfo.id)
				_this.ajax.getaddbyuserid(data).then(res => {
					const rs = res.data;
					if(rs.code == 200) {
						_this.cityList = rs.data
						_this.cityList.forEach(function(val, i) {
							if(val.isDefault == 1) {
								_this.inNum = val.id
							}
						})
					} else {
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error);
				});
			},
			infos() { //商品信息
				const _this = this
				console.log(_this.carids)
				let data = {
					"": _this.carids
				}
				_this.ajax.getcarbyids(data).then(res => {
					const rs = res.data
					if(rs.code == 200) {
						_this.tableData = rs.data
						_this.count()
					} else {
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error)
				})
			},
			count() {
				let total = 0; //临时总价
				this.tableData.forEach(function(val, index) {
					total += val.num * val.price; //累计总价
				});
				this.totalPrices = parseFloat(total);
			},
			addCity() { //新增地址
				const _this = this
				for(let i in _this.forms) {
					if(_this.forms[i] == '') {
						_this.showMsg('请填写完整', '2');
						return
					}
				}
				let defa = 0
				if(_this.defaults) {
					defa = 1
				} else {
					defa = 2
				}
				let data = {
					UserId: _this.userInfo.id,
					UserName: _this.forms.name,
					UserPhone: _this.forms.phone,
					AddressDetail: _this.forms.city,
					isDefault: defa
				}
				_this.ajax.createaddress(data).then(res => {
					const rs = res.data;
					if(rs.code == 200) {
						_this.centerDialogLogin = false
						_this.showMsg('添加成功');
						this.infos()
					} else {
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error);
				});
			},
			setting1() {
				this.forms.defaults = !this.forms.defaults
			},
			changeCount() { //账号
				let _this = this
				var reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
				if(_this.forms.phone == '') {
					//_this.formRed[0].phone = '请输入手机号码';
				} else if(!reg.test(_this.forms.phone)) {
					//_this.formRed[0].phone = '请输入正确的手机号';
				} else {
					//_this.formRed[0].phone = '';
				}
			},
			count() {
				let total = 0; //临时总价
				let snum = 0;
				this.tableData.forEach(function(val, index) {
					total += val.num * val.price; //累计总价
					snum += val.num
				});
				this.totalNum = snum
				this.totalPrices = parseFloat(total);
			},
			acvites(val) {
				const _this = this
				_this.inNum = val
			},
		},
	}
</script>

<style>
	.top_title {
		height: 49px;
		line-height: 49px;
		width: 100%;
		margin: 21px 0;
	}
	
	.top_title h3 {
		color: #000;
		font-size: 18px;
		float: left;
	}
	
	.top_title .btn_citys {
		float: right;
		width: 138px;
		height: 49px;
		background: #E6222A;
		color: #fff;
		cursor: pointer;
	}
	
	.address_ul {}
	
	.address_ul .ul_item {
		float: left;
		width: 372px;
		height: 160px;
		background: rgba(255, 255, 255, 1) url(../../assets/images/bg_shou.png) repeat-x;
		box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.05);
		position: relative;
		padding: 30px 39px 0;
		cursor: pointer;
		margin-right: 42px;
		margin-bottom: 30px;
	}
	
	.address_ul .ul_item:nth-child(3n) {
		margin-right: 0;
	}
	
	.address_ul .ul_item .item_img {
		position: absolute;
		right: 19px;
		top: 50%;
		margin-top: -12.5px;
	}
	
	.address_ul .ul_item h3 {
		font-size: 16px;
		font-weight: normal;
		height: 51px;
	}
	
	.address_ul .ul_item p {
		color: #7f7f7f;
		font-size: 13px;
		height: 20px;
		margin-bottom: 5px;
	}
	
	.address_ul .ul_item .city_add {
		width: 100%;
		height: 20px;
		overflow: hidden;
		/*超出的文本隐藏*/
		text-overflow: ellipsis;
		/*溢出用省略号显示*/
		white-space: nowrap;
		/*溢出不换行*/
	}
	
	.que_table {
		margin: 90px 0;
	}
	
	.que_table h3 {
		color: #000;
		font-size: 18px;
		margin-bottom: 35px;
	}
	
	.que_table .tab_img {
		float: left;
	}
	
	.que_table .tab_p {
		float: left;
		width: 60%;
		line-height: 20px;
		font-size: 14px;
		color: #585858;
		margin-left: 50px;
		margin-top: 43px;
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
	}
	
	.que_table .has-gutter th {
		color: #fff;
		background: #222831;
	}
	
	.settlement {
		height: 167px;
		background: #F5F5F5;
		margin-bottom: 18px;
		text-align: right;
		padding: 35px 0 0 0;
	}
	
	.settlement p {
		margin-bottom: 20px;
		color: #585858;
	}
	
	.settlement p span {
		text-align: left;
		display: inline-block;
		width: 70px;
	}
	
	.settlement p b {
		display: inline-block;
		width: 90px;
		text-align: left;
		margin-left: 60px;
		color: #585858;
		font-weight: normal;
	}
	
	.settlement .red {
		color: #E83239;
	}
	
	.btn_sumei {
		width: 124px;
		height: 43px;
		background: #E62129;
		color: #fff;
		font-size: 16px;
		float: right;
		cursor: pointer;
	}
	
	.receipt {
		width: 490px;
		position: fixed;
		left: 50%;
		top: 10%;
		border-radius: 5px;
		background: #fff;
		margin-left: -245px;
		z-index: 9999;
		padding: 0 54px 60px;
	}
	
	.receipt .receipt_title {
		text-align: center;
		position: relative;
		padding: 45px 0 20px;
		margin-bottom: 40px;
		border-bottom: 1px solid #f1f1f1;
	}
	
	.receipt .receipt_title h3 {
		font-weight: normal;
		font-size: 16px;
		line-height: 20px;
		color: #E6222A;
	}
	
	.receipt .receipt_title span {
		position: absolute;
		top: 10px;
		height: 1px;
		background: #707070;
		width: 95px;
	}
	
	.receipt .con_item {
		width: 307px;
		margin: 0 auto 20px;
		position: relative;
	}
	
	.receipt .con_item .inp_text {
		display: block;
		padding: 13px 0 13px 17px;
		height: 42px;
		font-size: 16px;
		border-radius: 5px;
		border: 1px solid #f3f3f3;
		width: 100%;
	}
	
	.receipt .inp_but {
		width: 245px;
		height: 49px;
		box-shadow: 0px 3px 15px rgba(230, 33, 41, 0.4);
		border-radius: 5px;
		background: #E62129;
		display: block;
		color: #fff;
		cursor: pointer;
		display: block;
		margin: 0 auto;
	}
	
	.receipt .con_item .area_text {
		width: 382px;
		height: 115px;
		border: 1px solid #F3F3F3;
		border-radius: 5px;
		color: #AFAFAF;
		padding: 20px 17px;
	}
	
	.receipt .con_item .sp_img {
		display: block;
		color: #585858;
		margin-bottom: 12px;
	}
	
	input::-webkit-input-placeholder {
		/* placeholder颜色  */
		color: #AFAFAF;
		/* placeholder字体大小  */
		font-size: 12px;
		/* placeholder位置  */
		text-align: left;
	}
	
	.receipt_close {
		position: absolute;
		right: 0;
		top: 52px;
		font-size: 18px;
		font-weight: bold;
		margin-left: -7px;
		color: #000;
	}
	
	.receipt .bot_left {
		float: left;
		font-size: 12px;
		color: #585858;
	}
	
	.receipt .bot_left span {
		width: 14px;
		height: 14px;
		margin-right: 2px;
		display: block;
		float: left;
		margin-top: 1px;
	}
	
	.receipt .bot_left span img {
		width: 100%;
		height: 100%;
	}
</style>


