<template>
	<div class="conMain">
		<div class="width12 clearfix">
			<div class="main_lefts">
				<p class="title">您的位置：首页>个人中心><span>{{urlName}}</span></p>
				<div class="main_lefts_pic">
					<span class="pic_img"><img src="../../assets/images/xiao.png"/></span>
					<p>-只是忘了怎么笑</p>
				</div>
				<ul class="main_nav">
					<li :class="{li_active:path == '/editData'?true:false}">
						<router-link to="/editData">编辑资料</router-link>
					</li>
					<li :class="{li_active:path == '/Order'?true:false}">
						<router-link to="/Order">我的订单</router-link>
					</li>
					<li :class="{li_active:path == '/receiptCity'?true:false}">
						<router-link to="/receiptCity">收货地址</router-link>
					</li>
				</ul>
			</div>
			<router-view></router-view>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'personal',
		data() {
			return {
				dateValue: '',
				inName: '',
				textValue: '',
				radios: '',
				path: '',
				urlName: ''
			}
		},
		watch: {
			$route(to, from) {
				this.path = this.$route.path;
				if(sessionStorage.getItem('userInfo') == undefined && (sessionStorage.getItem('userInfo') == null || sessionStorage.getItem('userInfo') == '')) {
					this.$router.push({
						path: '/',
					})
					this.$root.$emit("login", 1)
				}
				this.urlData()
			}
		},
		mounted() {
			this.path = this.$route.path
			if(sessionStorage.getItem('userInfo') == undefined && (sessionStorage.getItem('userInfo') == null || sessionStorage.getItem('userInfo') == '')) {
				this.$router.push({
					path: '/',
				})
				this.$root.$emit("login", 1)
			}
			this.urlData()
		},
		methods: {
			urlData() {
				const _this = this
				switch(_this.path) {
					case '/editData':
						_this.urlName = '编辑资料'
						break;
					case '/Order':
						_this.urlName = '我的订单'
						break;
					case '/receiptCity':
						_this.urlName = '收货地址'
						break;
				}
			}
		}
	}
</script>

<style>
	.conMain .title {
		height: 58px;
		line-height: 58px;
		color: #AFAFAF;
	}
	
	.conMain .title span {
		color: #E62129;
	}
	
	.main_lefts {
		width: 243px;
		float: left;
	}
	
	.main_lefts .main_lefts_pic {
		width: 100%;
		height: 203px;
		box-shadow: 0px -10px 15px rgba(154, 154, 154, 0.16);
		background: #fff;
		padding-top: 40px;
	}
	
	.main_lefts .main_lefts_pic .pic_img {
		display: block;
		width: 90px;
		height: 90px;
		border-radius: 100%;
		margin: 0 76px 16px;
	}
	
	.main_lefts .main_lefts_pic .pic_img img {
		width: 100%;
		height: 100%;
		border-radius: 100%;
	}
	
	.main_lefts .main_lefts_pic p {
		color: #585858;
		text-align: center;
		height: 24px;
		font-size: 18px;
		line-height: 24px;
	}
	
	.main_lefts .main_nav li {
		height: 58px;
		line-height: 58px;
		font-size: 16px;
		text-align: center;
		cursor: pointer;
	}
	
	.main_lefts .main_nav li a {
		display: block;
		height: 100%;
		width: 100%;
		color: #585858;
	}
	
	.main_lefts .main_nav .li_active a,
	.main_lefts .main_nav li:hover a {
		background: #FFEEEE;
		color: #E62129;
	}
	
	.main_rig {
		width: 934px;
		margin-left: 22px;
		float: left;
		padding-top: 38px;
		padding-bottom: 135px;
	}
</style>


