<template>
	<div class="main_rig">
		<div class="top_title">
			<h3>选择收货地址</h3>
			<button type="button" class="btn_citys" @click="centerDialogLogin = true">
				<i class="el-icon-plus"></i>
				添加收货地址
			</button>
		</div>
		<div class="fengexian"></div>
		<div class="mainp_rig_con">
			<div class="ul_item" v-for="(item,index) in cityList" :key="index">
				<h3>{{item.userName}}</h3>
				<p class="phone">{{item.userPhone}}</p>
				<p class="city_add">{{item.addressDetail}}</p>
				<div class="ul_item_bot clearfix">
					<a href="javascript:;" @click="setting(item.id)" class="bot_left">
						<span><img :src="inNum==item.id?imgsUrl:imgsUrl1"/></span> 设为默认地址
					</a>
					<div class="bot_right">
						<a href="javascript:;" @click="close_city(item.id)" class="ri_close">
							<i class="el-icon-delete"></i> 删除
						</a>
						<a href="javascript:;" @click="edit_data(item.id)" class="ri_edit">
							<i class="el-icon-edit-outline"></i> 编辑
						</a>
					</div>
				</div>
			</div>
		</div>
		<div class="tc_model" v-if="centerDialogLogin || centerDialogLogins"></div>
		<div class="receipt" v-if="centerDialogLogin">
			<div class="receipt_title">
				<h3>添加收货地址</h3>
				<a href="javascript:;" class="receipt_close" @click="centerDialogLogin=false">
					<i class="el-icon-close"></i>
				</a>
			</div>
			<div class="receipt_con">
				<p class="con_item">
					<span class="sp_img">收货人</span>
					<input type="text" class="inp_text" v-model="forms.name" placeholder="请输入收货人姓名或称呼 如：李先生" />
				</p>
				<p class="con_item">
					<span class="sp_img">手机号</span>
					<input type="text" class="inp_text" @change="changeCount" maxlength="11" v-model="forms.phone" placeholder="请输入收货人手机号" />
				</p>
				<p class="con_item">
					<span class="sp_img">收货地址</span>
					<input type="text" class="inp_text" v-model="forms.city" placeholder="请输入收货详细地址" />
				</p>
				<p class="con_item clearfix">
					<a href="javascript:;" @click="setting1" class="bot_left">
						<span><img :src="defaults?imgsUrl:imgsUrl1"/></span> 设为默认地址
					</a>
				</p>
				<button type="button" @click="addCity" class="inp_but">确认</button>
			</div>
		</div>
		<div class="receipt" v-if="centerDialogLogins">
			<div class="receipt_title">
				<h3>编辑收货地址</h3>
				<a href="javascript:;" class="receipt_close" @click="centerDialogLogins=false">
					<i class="el-icon-close"></i>
				</a>
			</div>
			<div class="receipt_con">
				<p class="con_item">
					<span class="sp_img">收货人</span>
					<input type="text" class="inp_text" v-model="form.name" placeholder="请输入收货人姓名或称呼 如：李先生" />
				</p>
				<p class="con_item">
					<span class="sp_img">手机号</span>
					<input type="text" class="inp_text" @change="changeCount" maxlength="11" v-model="form.phone" placeholder="请输入收货人手机号" />
				</p>
				<p class="con_item">
					<span class="sp_img">收货地址</span>
					<input type="text" class="inp_text" v-model="form.city" placeholder="请输入收货详细地址" />
				</p>
				<p class="con_item clearfix">
					<a href="javascript:;" @click="setting2" class="bot_left">
						<span><img :src="defaults1?imgsUrl:imgsUrl1"/></span> 设为默认地址
					</a>
				</p>
				<button type="button" @click="editCity" class="inp_but">确认</button>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'receiptCity',
		data() {
			return {
				imgsUrl: require('../../assets/images/yxz.png'),
				imgsUrl1: require('../../assets/images/wxz.png'),
				inNum: 0,
				dateValue: '',
				textValue: '',
				radios: '',
				centerDialogLogin: false,
				centerDialogLogins: false,
				forms: {
					name: '',
					phone: '',
					city: '',
				},
				form: {
					id:'',
					name: '',
					phone: '',
					city: '',
				},
				userInfo: '',
				defaults1: false,
				defaults: false,
				cityList:[],
			}
		},
		mounted(){
			let userInfo = sessionStorage.getItem('userInfo')
			this.userInfo = JSON.parse(userInfo)
			this.infos()
		},
		methods: {
			changeCount() { //账号
				let _this = this
				var reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
				if(_this.forms.phone == '') {
					//_this.formRed[0].phone = '请输入手机号码';
				} else if(!reg.test(_this.forms.phone)) {
					//_this.formRed[0].phone = '请输入正确的手机号';
				} else {
					//_this.formRed[0].phone = '';
				}
			},
			addCity() {//新增地址
				const _this = this
				for(let i in _this.forms) {
					if(_this.forms[i] == ''){
						_this.showMsg('请填写完整', '2');
						return
					}
				}
				let defa = 0
				if(_this.defaults) {
					defa = 1
				} else {
					defa = 2
				}
				let data = {
					UserId: _this.userInfo.id,
					UserName: _this.forms.name,
					UserPhone: _this.forms.phone,
					AddressDetail: _this.forms.city,
					isDefault: defa
				}
				_this.ajax.createaddress(data).then(res => {
					const rs = res.data;
					if(rs.code == 200) {
						_this.centerDialogLogin = false
						_this.showMsg('添加成功');
						this.infos()
					} else {
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error);
				});
			},
			editCity(){//编辑地址
				const _this =this
				for(let i in _this.form) {
					if(_this.form[i] == ''){
						console.log(_this.form[i])
						_this.showMsg('请填写完整', '2');
						return
					}
				}
				let defa = 0
				if(_this.defaults1) {
					defa = 1
				} else {
					defa = 2
				}
				let data = {
					UserId:_this.userInfo.id,
					id:_this.form.id,
					UserName:_this.form.name,
					UserPhone:_this.form.phone,
					AddressDetail:_this.form.city,
					isDefault:defa,
				}
				_this.ajax.upaddress(data).then(res=>{
					const rs = res.data
					if(rs.code == 200){
						_this.centerDialogLogins = false
						_this.showMsg('编辑成功');
						_this.infos()
					}else{
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error){
					console.log(error)
				})
			},
			setting(val) {
				const _this = this
				_this.inNum = val
				let defa = 0
				let data = {
					UserId:_this.userInfo.id,
					id:val,
					isDefault:1,
				}
				_this.ajax.updefault(data).then(res=>{
					const rs = res.data
					if(rs.code == 200){
						_this.showMsg('修改成功');
						_this.infos()
					}else{
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error){
					console.log(error)
				})
			},
			setting1() {
				this.defaults = !this.defaults
			},
			setting2() {
				this.defaults1 = !this.defaults1
			},
			close_city(val) {
				const _this = this
				let data = {
					'':val
				}
				_this.ajax.deladdress(data).then(res =>{
					const rs = res.data
					if(rs.code == 200){
						_this.showMsg('删除成功');
						_this.infos()
					}else{
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error){
					console.log(error)
				})
			},
			edit_data(sid) {
				const _this = this
				_this.centerDialogLogins=true
				_this.cityList.forEach(function(val, i){
					if(val.id == sid){
						_this.form.id=val.id
						_this.form.name=val.userName
						_this.form.city=val.addressDetail
						_this.form.phone=val.userPhone
						if(val.isDefault == 1){
							_this.defaults1 =true
						}else{
							_this.defaults1 =false
						}
					}
				})
			},
			infos(){
				const _this = this
				let data = {
					'': _this.userInfo.id,
				}
				console.log(_this.userInfo.id)
				_this.ajax.getaddbyuserid(data).then(res => {
					const rs = res.data;
					if(rs.code == 200) {
						_this.cityList = rs.data
						_this.cityList.forEach(function(val,i){
							if(val.isDefault == 1 ){
								_this.inNum = val.id
							}
						})
					} else {
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error);
				});
			}
		}
	}
</script>

<style>
	.main_rig {
		width: 934px;
		margin-left: 22px;
		float: left;
		padding-top: 38px;
		padding-bottom: 135px;
	}
	
	.main_rig {
		font-size: 18px;
		color: #000;
	}
	
	.main_rig .top_title {
		height: 49px;
		line-height: 49px;
		width: 100%;
		margin: 21px 0;
	}
	
	.main_rig .top_title h3 {
		color: #000;
		font-size: 18px;
		float: left;
	}
	
	.main_rig .top_title .btn_citys {
		float: right;
		width: 138px;
		height: 49px;
		background: #E6222A;
		color: #fff;
		cursor: pointer;
	}
	
	.main_rig .mainp_rig_con {
		position: relative;
	}
	
	.main_rig .mainp_rig_con .ul_item {
		float: left;
		width: 452px;
		height: 160px;
		background: rgba(255, 255, 255, 1) url(../../assets/images/bg_shou.png) repeat-x;
		box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.05);
		position: relative;
		padding: 30px 39px 0;
		cursor: pointer;
		margin-right: 30px;
		margin-bottom: 30px;
	}
	
	.main_rig .mainp_rig_con .ul_item:nth-child(2n) {
		margin-right: 0;
	}
	
	.main_rig .mainp_rig_con .ul_item .item_img {
		position: absolute;
		right: 19px;
		top: 50%;
		margin-top: -12.5px;
	}
	
	.main_rig .mainp_rig_con .ul_item h3 {
		font-size: 16px;
		font-weight: normal;
		height: 40px;
	}
	
	.main_rig .mainp_rig_con .ul_item p {
		color: #7f7f7f;
		font-size: 13px;
		height: 20px;
	}
	
	.main_rig .mainp_rig_con .ul_item .city_add {
		width: 100%;
		height: 20px;
		overflow: hidden;
		/*超出的文本隐藏*/
		text-overflow: ellipsis;
		/*溢出用省略号显示*/
		white-space: nowrap;
		/*溢出不换行*/
	}
	
	.ul_item_bot {
		margin-top: 25px;
	}
	
	.bot_left {
		float: left;
		font-size: 12px;
		color: #585858;
	}
	
	.bot_left span {
		width: 14px;
		height: 14px;
		margin-right: 2px;
		display: block;
		float: left;
		margin-top: 1px;
	}
	
	.bot_left span img {
		width: 100%;
		height: 100%;
	}
	
	.bot_right {
		float: right;
		display: flex;
		width: 100px;
	}
	
	.bot_right a {
		color: #AFAFAF;
		font-size: 12px;
		flex-grow: 1;
	}
	
	.bot_right .ri_close {
		margin-right: 20px;
	}
	
	.bot_right .ri_edit {
		color: #E62129;
	}
	
	.receipt {
		width: 490px;
		position: fixed;
		left: 50%;
		top: 10%;
		border-radius: 5px;
		background: #fff;
		margin-left: -245px;
		z-index: 9999;
		padding: 0 54px 60px;
	}
	
	.receipt .receipt_title {
		text-align: center;
		position: relative;
		padding: 45px 0 20px;
		margin-bottom: 40px;
		border-bottom: 1px solid #f1f1f1;
	}
	
	.receipt .receipt_title h3 {
		font-weight: normal;
		font-size: 16px;
		line-height: 20px;
		color: #E6222A;
	}
	
	.receipt .receipt_title span {
		position: absolute;
		top: 10px;
		height: 1px;
		background: #707070;
		width: 95px;
	}
	
	.receipt .con_item {
		width: 307px;
		margin: 0 auto 20px;
		position: relative;
	}
	
	.receipt .con_item .inp_text {
		display: block;
		padding: 13px 0 13px 17px;
		height: 42px;
		font-size: 16px;
		border-radius: 5px;
		border: 1px solid #f3f3f3;
		width: 100%;
	}
	
	.receipt .inp_but {
		width: 245px;
		height: 49px;
		box-shadow: 0px 3px 15px rgba(230, 33, 41, 0.4);
		border-radius: 5px;
		background: #E62129;
		display: block;
		color: #fff;
		cursor: pointer;
		display: block;
		margin: 0 auto;
	}
	
	.receipt .con_item .area_text {
		width: 382px;
		height: 115px;
		border: 1px solid #F3F3F3;
		border-radius: 5px;
		color: #AFAFAF;
		padding: 20px 17px;
	}
	
	.receipt .con_item .sp_img {
		display: block;
		color: #585858;
		margin-bottom: 12px;
	}
	
	input::-webkit-input-placeholder {
		/* placeholder颜色  */
		color: #AFAFAF;
		/* placeholder字体大小  */
		font-size: 12px;
		/* placeholder位置  */
		text-align: left;
	}
	
	.receipt_close {
		position: absolute;
		right: 0;
		top: 52px;
		font-size: 18px;
		font-weight: bold;
		margin-left: -7px;
		color: #000;
	}
</style>


