<template>
	<div class="main">
		<div class="width12">
			<div class="title">您的位置：首页>个人中心><span>订单详情</span></div>
			<div class="fengexian"></div>
			<div class="main_con">
				<h4>订单信息</h4>
				<div class="steps">
					<el-steps :space="200" :active="tableData.orderState" align-center>
						<el-step title="待付款" :description="tableData.createDate"></el-step>
						<el-step title="待发货" description=""></el-step>
						<el-step title="待收货" description=""></el-step>
						<el-step title="待评价" description=""></el-step>
						<el-step title="已完成" description=""></el-step>
					</el-steps>
				</div>
				<h4>收货信息</h4>
				<div class="receipt_xx">
					<h5>{{tableData.orderAddressInfo.userName}}</h5>
					<p>
						<span><img width="10" height="14" src="../../assets/images/phone.png"/><font>{{tableData.orderAddressInfo.userPhone}}</font></span>
						<span><img width="14" height="13" src="../../assets/images/code.png"/><font>000000</font></span>
						<span>{{tableData.orderAddressInfo.addressDetail}}</span>
					</p>
					<div class="orderh">订单号：<span>{{tableData.orderCode}}</span></div>
				</div>
				<div class="que_table clearfix">
					<h3>确认商品信息</h3>
					<el-table :data="tableData.orderProducts" stripe style="width: 100%">
						<el-table-column label="商品信息" min-width="49%">
							<template slot-scope="scope">
								<img v-lazy="scope.row.proCover" alt="封面" width="120" height="120" class="tab_img" />
								<p class="tab_p">{{ scope.row.proName }}</p>
							</template>
						</el-table-column>
						<el-table-column prop="proPrice" label="单价(元）" min-width="17%">
						</el-table-column>
						<el-table-column prop="proNum" label="数量" min-width="17%">
						</el-table-column>
						<el-table-column prop="proTotalPrice" label="金额" min-width="17%">
						</el-table-column>
					</el-table>
					<div class="settlement clearfix">
						<p>
							<span>商品总计：</span>
							<b>¥{{tableData.totalPrice}}</b>
						</p>
						<p>
							<span>数量：</span>
							<b>{{tableData.totals}}</b>
						</p>
						<p>
							<span>总价：</span>
							<b class="red">¥{{tableData.totalPrice}}</b>
						</p>
						<a href="jiavascript:;" v-if="tableData.orderState==1" class="btn_pay">取消订单</a>
					</div>
				</div>
				
			</div>			
		</div>
	</div>
</template>

<script>
	export default {
		name: 'Order',
		data() {
			return {
				tableData: {},
				total: 11,
				currentPage: 1,
				dialogVisible: false,
				centerDialogLogin:false,
				totalPrice: 0,
				orderId:'',
			}
		},
		watch: {
			$route(to, from) {
				if(sessionStorage.getItem('userInfo') == undefined && (sessionStorage.getItem('userInfo') == null || sessionStorage.getItem('userInfo') == '')) {
					this.$router.push({
						path: '/',
					})
					this.$root.$emit("login", 1)
				}
				this.orderId = this.$route.query.orderId
			}
		},
		created() {
			this.orderId = this.$route.query.orderId
		},
		mounted() {
			if(sessionStorage.getItem('userInfo') == undefined && (sessionStorage.getItem('userInfo') == null || sessionStorage.getItem('userInfo') == '')) {
				this.$router.push({
					path: '/',
				})
				this.$root.$emit("login", 1)
			}
			this.orderId = this.$route.query.orderId
			let userInfo = sessionStorage.getItem('userInfo')
			this.userInfo = JSON.parse(userInfo)
			this.infos();
		},
		methods: {
			infos(){
				const _this = this
				let meks = {
					OrderId:_this.orderId,
					UserId:_this.userInfo.id,
				}
				_this.ajax.orderDetails(meks).then(res=>{
					const rs = res.data
					if(rs.code == 200){
						let totals = 0
						_this.tableData = rs.data
						 this.tableData.orderProducts.forEach(function (val,index) {
		                    totals += val.proNum
		                });
		                _this.tableData.totals = totals
					}else{
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error){
					console.log(error)
				})
			},
			handleSizeChange(val) {
				console.log(`每页 ${val} 条`);
			},
			handleCurrentChange(val) {
				console.log(`当前页: ${val}`);
			},
			count() {
                let total = 0;//临时总价
                this.tableData.orderProducts.forEach(function (val,index) {
                    total += val.num*val.price;//累计总价
                });
                this.totalPrice =parseFloat(total);
          	},
		}
	}
</script>

<style>
	.main .title {
		height: 58px;
		line-height: 58px;
		color: #AFAFAF;
	}
	.main .title span {
		color: #E62129;
	}
	.main_con h4 {
		font-size: 18px;
		color: #000;
	}
	.main_con .steps{
		width: 1000px;
		margin: 90px auto 62px;
	}
	.main_con .steps .el-step__title{
	    position: absolute;
	    left: 0;
	    top: -38px;
	    width: 100%;
	    color: #585858;
	    font-weight: normal;
	}
	.main_con .steps .el-step__description.is-finish{
		color: #AFAFAF;
	}
	.main_con .steps .el-step.is-center .el-step__description{
		margin-top: 10px;
	}
	.main_con .steps .el-step__icon.is-text{
		width:46px;
		height:16px;
		border-radius:12px;
		background: #EEEEEE;
		border: none;
		color: #fff;
	}
	.main_con .steps .el-step.is-horizontal .el-step__line{
		top: 6px;
		background: #eee;
		height: 6px;
	}
	.main_con .steps .el-step__head.is-finish{
		color: #eee;
		background-color: transparent !important;
		border: none;
	}
	.main_con .steps .is-finish .el-step__icon.is-text{
		background:#E62129;
	}
	.receipt_xx{
		background: url(../../assets/images/bg_shou.png) repeat-x;
		width:1200px;
		height:140px;
		padding: 30px 39px;
		margin-top: 22px;
		box-shadow:0px 3px 20px rgba(0,0,0,0.05);
	}
	.receipt_xx h5{
		font-size: 16px;
		color: #000;
	}
	.receipt_xx p{
		color: #585858;	
		font-size: 14px;
		margin: 14px 0;
	}
	.receipt_xx p span{
		margin-right: 23px;
	}
	.receipt_xx p span:last-child{
		display: inline-block;
		width: 75%;
		height: 16px;
		overflow: hidden;
		text-overflow: ellipsis;
		/*溢出用省略号显示*/
		white-space: nowrap;
		/*溢出不换行*/
	}
	.receipt_xx p span img{
		margin-right: 9px;
		margin-top: 2px;
		vertical-align: sub;
	}
	.receipt_xx .orderh{
		color: #E62129;
		font-size: 12px;
	}
	.que_table{
		margin: 90px 0;
	}
	.que_table h3{
		color: #000;
		font-size: 18px;
		margin-bottom: 35px;
	}
	.el-table{
		box-shadow:0px 3px 20px rgba(0,0,0,0.05);
	}
	.que_table .tab_img{
		float: left;
	}
	.que_table .tab_p{
		float: left;
		width: 60%;
		line-height: 20px;
		font-size: 14px;
		color: #585858;
		margin-left: 50px;
		margin-top: 43px;
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
	}
	.que_table .has-gutter th{
		color: #fff;
		background: #222831;
	}
	.settlement {
		background: #F5F5F5;
		margin-bottom: 18px;
		text-align: right;
		padding: 35px 0 20px 0;
	}
	.settlement p{
		margin-bottom: 20px;
		color: #585858;
	}
	.settlement p span{
		text-align: left;
		display: inline-block;
		width: 70px;
	}
	.settlement p b{
		display: inline-block;
		width: 90px;
		text-align: left;
		margin-left: 60px;
		color: #585858;
		font-weight: normal;
	}
	.settlement .red{
		color: #E83239;
	}
	.settlement .btn_pay {
		background: #E62129;
		color: #fff;
		width: 124px;
		height: 43px;
		display: block;
		text-align: center;
		line-height: 43px;
		font-size: 14px;
		margin: 0 30px;
		float: right;
	}
</style>


