<template>
	<div class="main_rig">
		<h4>编辑资料</h4>
		<div class="fengexian"></div>
		<div class="mainp_rig_con">
			<p class="con_title">基本信息</p>
			<div class="mainp_rig_con_pic">
				<img :src="imgList!=''?imgList:imgUrs" />
				<p>点击修改头像</p>
				<input type="file" name="file_img" @change="fileChange($event)" class="file_img" id="file_img" multiple="multiple" accept="image/jpg,image/jpeg,image/png" />
			</div>
			<p class="con_item">
				<span class="sp">昵称</span>
				<input type="text" name="" v-model="form.inName" class="item_inp" />
			</p>
			<p class="con_item">
				<span class="sp">手机号</span>
				<span class="phone">{{form.phone}}</span>
			</p>
			<p class="con_item">
				<span class="sp">性别</span>
				<el-radio-group v-model="form.radios">
					<el-radio :label="1">男</el-radio>
					<el-radio :label="2">女</el-radio>
				</el-radio-group>
			</p>
			<p class="con_item">
				<span class="sp">出生日期</span>
				<span class="chus">
					<el-date-picker
				      v-model="form.dateValue"
				      type="date"
				      placeholder="选择日期">
				    </el-date-picker>
				</span>
			</p>
			<p class="con_item">
				<span class="sp">个性签名</span>
				<textarea name="" class="item_text" maxlength="300" v-model="form.textValue"></textarea>
			</p>
			<button type="button" class="btn_bc" @click="modifys">保存</button>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'personal',
		data() {
			return {
				form: {
					dateValue: '',
					inName: '',
					textValue: '',
					radios: '',
					phone: '',
				},
				userInfo: {},
				imgList:'',
				imgLists:'',
				imgUrs: require('../../assets/images/datu.png'),
			}
		},
		mounted() {
			let userInfo = sessionStorage.getItem('userInfo')
			this.userInfo = JSON.parse(userInfo)
			this.info()
		},
		methods: {
			info() {
				let _this = this
				let data = {
					'': _this.userInfo.id
				}
				_this.ajax.basicUser(data).then(res => {
					const rs = res.data;
					if(rs.code == 200) {
						if(rs.data != null && rs.data != '') {
							_this.form.inName = rs.data.userName
							_this.form.dateValue = rs.data.userBirthday
							_this.form.textValue = rs.data.userSignature
							_this.form.radios = rs.data.userSex
							_this.form.phone = rs.data.userPhone
							_this.imgList = _this.apiUrl+rs.data.userPhoto
						}
					} else {
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error);
				});
			},
			modifys() {
				let _this = this
				let data = {
					UserName: _this.form.inName,
					UserSex: _this.form.radios,
					UserBirthday: _this.form.dateValue,
					UserPhone: _this.form.phone,
					UserSignature: _this.form.textValue,
					UserPhoto:_this.imgLists,
					Id: _this.userInfo.id,
				}
				_this.ajax.modifyUser(data).then(res => {
					const rs = res.data;
					if(rs.code == 200) {
						_this.showMsg(rs.msg)
						this.info()
					} else {
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error);
				});
			},
			fileChange(el) {
				const _that = this
				if(!el.target.files[0].size) return;
				//this.fileList(el.target);
				//el.target.value = ''
				//console.log(el.target.files[0])
				const imgData = new FormData();
				imgData.append('file', el.target.files[0]);
				imgData.append('buyerId', _that.userInfo.id);
				_that.ajax.updateImg(imgData).then(res => {
					const rs = res.data;
					if(rs.code == 200) {
						_that.imgList=_that.apiUrl+rs.data.fileUrl
						_that.imgLists=rs.data.fileUrl
						_that.showMsg(rs.msg);
						console.log(_that.imgList);
						_that.modifys()
					} else {
						_that.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {});
			},
		}
	}
</script>

<style>
	.main_rig {
		width: 934px;
		margin-left: 22px;
		float: left;
		padding-top: 38px;
		padding-bottom: 135px;
	}
	
	.main_rig h4 {
		font-size: 18px;
		color: #000;
	}
	
	.main_rig .mainp_rig_con {
		position: relative;
	}
	
	.main_rig .mainp_rig_con .con_title {
		height: 48px;
		line-height: 48px;
		font-size: 16px;
		color: #585858;
	}
	
	.main_rig .mainp_rig_con .con_item {
		display: flex;
		margin: 24px 0;
		align-items: center;
	}
	
	.main_rig .mainp_rig_con .con_item .sp {
		width: 74px;
		text-align: right;
		font-size: 14px;
		color: #AFAFAF;
		margin-right: 32px;
	}
	
	.main_rig .mainp_rig_con .con_item .item_inp {
		width: 210px;
		height: 46px;
		background: #F8F8F8;
		border-radius: 3px;
		padding: 3px 5px;
	}
	
	.main_rig .mainp_rig_con .con_item .el-input__inner {
		background: #F8F8F8;
		border-radius: 3px;
		border: none;
	}
	
	.main_rig .mainp_rig_con .con_item .el-input__prefix {
		left: initial;
		right: 0;
	}
	
	.main_rig .mainp_rig_con .con_item .el-radio__input.is-checked .el-radio__inner {
		background: #E62129;
		border-color: #E62129;
	}
	
	.main_rig .mainp_rig_con .con_item .el-radio__label {
		color: #AFAFAF;
	}
	
	.main_rig .mainp_rig_con .con_item .item_text {
		width: 819px;
		height: 142px;
		background: #F8F8F8;
		border-radius: 3px;
		padding: 10px;
	}
	
	.main_rig .mainp_rig_con .con_item .phone {
		color: #585858;
		font-size: 14px;
	}
	
	.mainp_rig_con_pic {
		position: absolute;
		right: 0;
		top: 20px;
		color: #E62129;
		cursor: pointer;
	}
	
	.mainp_rig_con_pic img {
		width: 155px;
		height: 155px;
		border-radius: 5px;
		display: block;
		cursor: pointer;
	}
	
	.mainp_rig_con_pic p {
		line-height: 30px;
		text-align: center;
		font-size: 14px;
		cursor: pointer;
	}
	
	.mainp_rig_con_pic input {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		cursor: pointer;
	}
	
	.btn_bc {
		border: none;
		display: block;
		float: right;
		width: 100px;
		height: 38px;
		background: #E62129;
		opacity: 1;
		border-radius: 2px;
		font-size: 14px;
		text-align: center;
		color: #fff;
	}
</style>


