import Vue from 'vue'
import axios from 'axios'
import qs from 'qs'
import router from '@/router'

const vm = new Vue
const instance = axios.create({
	//baseURL: 'http://39.104.19.127:8080',
	baseURL:'http://192.168.1.78/',
})

//加载动画
let _loading;
Vue.prototype.showLoad = function (obj){
  var obj = obj||'拼命加载中';
  _loading = this.$loading({
    lock: true,
    text:obj,
    spinner:"el-icon-loading",
    background:"rgba(0, 0, 0, 0.8)"
  })
};
//提示
Vue.prototype.showMsg = function (obj,type,fun){
  type = type||1;
  if(type==1){//成功
    this.$message({ message: obj,type: 'success'});
  }else if(type==2){//错误
    this.$message.error({ message: obj});
  } 
  setTimeout(fun,500);
};

//响应拦截器即异常处理
instance.interceptors.response.use(response => {
	console.log(response)
  if (response.data.code == '201') {
    window.sessionStorage.clear();
    Vue.prototype.showMsg(response.data.msg,'2',function(){
      router.push('/');
    });
    console.log(response.data.httpStatusCode)

	}else{
  	  setTimeout(function(){
      _loading&&_loading.close();
    },1000)
    return response
  }
}, err => {
  if (err && err.response) {
    setTimeout(function(){
      _loading&&_loading.close();
    },1000)
    switch (err.response.status) {
      case 400:
      console.log('错误请求');
      break;
      case 401:
      console.log('未授权，请重新登录');
      break;
      case 403:
      console.log('拒绝访问');
      break;
      case 404:
      console.log('请求错误,未找到该资源');
      break;
      case 405:
      console.log('请求方法未允许');
      break;
      case 408:
      console.log('请求超时');
      break;
      case 500:
      console.log('服务器端出错');
      break;
      case 501:
      console.log('网络未实现');
      break;
      case 502:
      console.log('网络错误');
      break;
      case 503:
      console.log('服务不可用');
      break;
      case 504:
      console.log('网络超时');
      break;
      case 505:
      console.log('http版本不支持该请求');
      break;
      default:
      console.log(`连接错误${err.response.status}`)
    }
  } else {
    console.log('连接到服务器失败')
  }
  return Promise.resolve(err.response)
});

export default instance





