<template>
	<div class="main">
		<div class="width12">
			<div class="main_comm clearfix">
				<p class="main_comm_title">您的位置：首页>商品分类><label>商品详情</label></p>
				<div class="goodDetails_name_img">
					<div class="good_top_img">
						<img :src="ImgUrl">
					</div>
					<div class="little_img clearfix">
						<a href="javascript:;" class="arrows arrow_left" @click="arrowLeft"><i class="el-icon-arrow-left"></i></a>
						<div class="little_img_ul_trom">
							<ul class="little_img_ul clearfix" ref="mybox">
								<li @click='getIndex(img.proImg)' v-for="img in imgUrl" class="little_img_li">
									<img :src="apiUrl+img.proImg">
								</li>
							</ul>
						</div>
						<a href="javascript:;" class="arrows arrow_right" @click="arrowRight"><i class="el-icon-arrow-right"></i></a>
					</div>
				</div>
				<div class="main_comm_con">
					<h3 class="title">{{proName}}</h3>
					<div class="price"><span class="sp_left">商品价格</span><b>￥{{commListMone!=''?commListMone[inNumsRed].priceValue:''}}</b></div>
					<div class="salesNum"><span class="sp_left">商品销量</span><label>{{sellNum}}件</label></div>
					<div class="colour clearfix">
						<span class="sp_left">商品颜色</span>
						<div class="colour_con">
							<a href="javascript:;" v-for="(item,index) in commListMone" :key="index" :class="{colour_con_item:true,item_active:inNumsRed==index?true:false}" @click="redClick(index)">{{item.priceKey}}</a>
						</div>
					</div>
					<div class="commNum clearfix">
						<span class="sp_left">商品数量</span>
						<div class="commNum_con">
							<a href="javascript:;" :class="{num_minus:true,item_active:flges1}" @click="numMinus"><i class="el-icon-minus"></i></a>
							<p class="nums">{{inNums}}</p>
							<a href="javascript:;" :class="{num_plus:true,item_active:flges2}" @click="numPlus"><i class="el-icon-plus"></i></a>
							<p class="wenben">件（库存{{repertoryNum}}件）</p>
						</div>
					</div>
					<div class="btn_con clearfix">
						<button type="button" :class="{btn_con_left:true,btn_con_det:repertoryNum==0?true:false}" disabled="repertoryNum==0?true:false" @click="buyNow">立即购买</button>
						<button type="button" :class="{btn_con_right:true,btn_con_det:repertoryNum==0?true:false}" disabled="repertoryNum==0?true:false" @click="addCart"><img :src="repertoryNum==0?cartUrl:cartUrl1" class="cart"/>加入购物车</button>
					</div>
				</div>
			</div>
			<div class="main_tab">
				<el-tabs type="card" @tab-click="handleClick">
					<el-tab-pane label="商品详情"></el-tab-pane>
					<el-tab-pane label="累计评论"></el-tab-pane>
				</el-tabs>
			</div>
			<div v-if="tabType == 0" v-html="dataContent"></div>
			<div v-if="tabType == 1" class="main_tab_pl">
				<h3>全部评论({{total}})</h3>
				<div class="main_tab_pl_con clearfix">
					<div class="main_tab_pl_con_item clearfix" v-for="(item,index) in plList" :key="index">
						<div class="item_pic"><img v-lazy="apiUrl+item.userPhoto" width="38" height="38" /></div>
						<div class="item_ri_con">
							<h4>{{item.userName}}</h4>
							<p>{{item.reviewContent}}</p>
							<!--手机拿到了，很不错，比想象中的要好。华为在细节上要比苹果差一点，有些细节还是比较粗糙，比如耳机数据线的包装，比起苹果看起来就像山寨的一样。系统界面针对屏幕的优化不够，在软件使用时，圆角跟软件不匹配，但是作为买的第一个安卓手机，总体满意。-->
							<div class="item_pic_in">
								<img v-lazy="apiUrl+items" v-for="items in item.proImgk" />
							</div>
							<div class="arrow_num clearfix">
								<time>{{(item.createDate!=null || item.createDate!='')&&item.createDate!=undefined?item.createDate.substring(0,10):''}}</time>
								<!--<a href="javascript:;" :class="{zand:true,zan_active:inZan==index?true:false}" @click="zanClick(index)"><img :src="inZan==index?zanUrl1:zanUrl" width="17" height="17" />54545</a>-->
							</div>
						</div>
					</div>
				</div>
				<div class="pages" v-if="total>10">
					<el-pagination 
						@size-change="handleSizeChange" 
						@current-change="handleCurrentChange" 
						:current-page.sync="currentPage" 
						background 
						:page-size="10" 
						layout="prev, pager, next, jumper" 
						:total="total">
					</el-pagination>
				</div>
			</div>
			<div class="main_bottom">
				<p>没有更多了
					<span></span>
					<span></span>
				</p>
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		name: 'commDetails',
		data() {
			return {
				id: this.$route.params.id,
				tabType: 0,
				proName: '',
				cartUrl: require('../../assets/images/cart.png'),
				cartUrl1: require('../../assets/images/cart_01.png'),
				imgUrl: [],
	             //大图片默认显示第一张
	            ImgUrl: '',
				sellNum: '', //销量
				repertoryNum: '', //库存
				commListMone: [],
				inNums: 1, //商品加减
				inNumsRed: 0, //商品颜色
				inZan: 0,
				inZans: false,
				flges1: false,
				flges2: false,
				currentPage: 5,
				dataContent: '',
				insts: 1,
				ProductId: '',
				total:0,
				pages:1,
				plList:[],
			}
		},
		mounted() {
			let userInfo = sessionStorage.getItem('userInfo')
			this.userInfo = JSON.parse(userInfo)
			this.info()
		},
		methods: {
			buyNow(){//立即购买
				const _this = this
				if(sessionStorage.getItem('userInfo') != undefined && (sessionStorage.getItem('userInfo') != null || sessionStorage.getItem('userInfo') != '')) {
					const _this = this
					let data = {
						ProductId: _this.ProductId,
						PriceId: _this.commListMone[_this.inNumsRed].id,
						TotalPrice: _this.commListMone[_this.inNumsRed].priceValue * _this.inNums,
						Num: _this.inNums,
						UserId: _this.userInfo.id
					}
					_this.ajax.addcar(data).then(res => {
						const rs = res.data;
						if(rs.code == 200) {
							_this.$router.push({
								name:'verify',
								params:{
									carids:rs.data
								}
							})
						} else {
							_this.showMsg(rs.msg, '2');
						}
					}).catch(function(error) {
						console.log(error);
					});
				} else {
					_this.showMsg('您还没有登录哦！快去登录吧！！！', '2');
					this.$root.$emit("login", 1)
				}
			},
			addCart() { //加入购物车
				const _this = this
				if(sessionStorage.getItem('userInfo') != undefined && (sessionStorage.getItem('userInfo') != null || sessionStorage.getItem('userInfo') != '')) {
					console.log(_this.inNums)
					let data = {
						ProductId: _this.ProductId,
						PriceId: _this.commListMone[_this.inNumsRed].id,
						TotalPrice: _this.commListMone[_this.inNumsRed].priceValue * _this.inNums,
						Num: _this.inNums,
						UserId: _this.userInfo.id
					}
					_this.ajax.addcar(data).then(res => {
						const rs = res.data;
						if(rs.code == 200) {
							_this.$root.$emit("cartNum", 1)
							_this.showMsg('成功加入,请到购物车查看...');
						} else {
							_this.showMsg(rs.msg, '2');
						}
					}).catch(function(error) {
						console.log(error);
					});
				} else {
					_this.showMsg('您还没有登录哦！快去登录吧！！！', '2');
					this.$root.$emit("login", 1)
				}
			},
			treview(){//评论列表
				const _this = this
				let data = {
					productId:_this.id,
					page:_this.pages,
					limit:10,
				}
				_this.ajax.getproductreview(data).then(res => {
					const rs = res.data;
					if(rs.code == 200) {
						_this.total = rs.data.totalCount
						_this.plList = rs.data.pageDatas
						rs.data.pageDatas.forEach(function(val, i) {
							let imgs = rs.data.pageDatas[i].imgIds.split(",")
							let imgk = []
							imgs.forEach(function(vals, k) {
								imgk.push(vals)
							});
							_this.plList[i].proImgk = imgk
						});
						console.log(_this.plList)
					} else {
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error);
				});
			},
			handleClick(tab, e) { //点击选项卡
				this.tabType = tab.index;
				var _val = tab.index; //
				if(_val == 1){
					this.treview()
				}
			},
			handleSizeChange(val) {
				console.log(`每页 ${val} 条`);
			},
			handleCurrentChange(val) {
				//console.log(`当前页: ${val}`);
				this.pages = val
			},
			//点击小图片时将图片路径赋值给大图
			getIndex(imgUrl) {
				this.ImgUrl = imgUrl;
			},
			arrowLeft() { //左滑动
				const _this = this
				let marl = _this.$refs.mybox.style.marginLeft
				//console.log(_this.imgUrl.length)
				let marginL = marl.substring(1, marl.length - 2)
				if(_this.insts == 1) {
					return
				}
				if(_this.insts > 0) {
					_this.insts--
						if(marginL > 85) {
							_this.$refs.mybox.style.marginLeft = -(marginL - 85) + 'px';
						} else {
							_this.$refs.mybox.style.marginLeft = marginL - 85 + 'px';
						}
				}
			},
			arrowRight() { //右滑动
				const _this = this
				//console.log(_this.imgUrl.length)
				console.log(_this.insts)
				if(_this.imgUrl.length - 5 == _this.insts) {
					return
				}
				if(_this.imgUrl.length > 5) {
					_this.$refs.mybox.style.marginLeft = '-' + _this.insts * 85 + 'px';
					_this.insts++
				}
			},
			numMinus() { //商品-减法
				const _this = this
				if(_this.inNums > 1) {
					_this.inNums -= 1
				}
				_this.flges1 = true
				_this.flges2 = false
			},
			numPlus() { //商品-加法
				const _this = this
				//if(_this.inNums > -1){
				_this.inNums += 1
				//}
				_this.flges1 = false
				_this.flges2 = true
			},
			redClick(val) { //商品颜色切换
				this.inNumsRed = val
			},
			zanClick(val) {
				this.inZan = val
				this.inZans = this.inZans
			},
			info() {
				const _that = this
				let data = {
					'': _that.id
				}
				_that.ajax.getprodetail(data).then(res => {
					const rs = res.data;
					if(rs.code == 200) {
						_that.dataContent = rs.data.imgText
						_that.ProductId = rs.data.proId
						_that.proName = rs.data.proName
						_that.commListMone = rs.data.prices
						_that.sellNum = rs.data.sellNum
						_that.repertoryNum = rs.data.repertoryNum
						_that.imgUrl = rs.data.proImgs
						_that.ImgUrl = _that.apiUrl + rs.data.proImgs[0].proImg
					} else {
						_that.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error);
				});
			},
		},
	}
</script>
<style>
	.main_comm_title {
		height: 16px;
		font-size: 12px;
		line-height: 16px;
		color: #AFAFAF;
		margin: 13px 0;
	}
	
	.main_comm_title label {
		color: #E62129;
	}
	
	.goodDetails_name_img {
		float: left;
		width: 500px;
		margin-right: 30px;
	}
	
	.good_top_img {
		width: 500px;
		height: 500px;
	}
	
	.goodDetails_name_img img {
		width: 100%;
		height: 100%
	}
	
	.little_img {
		width: 500px;
		height: 74px;
		margin: 20px 0;
	}
	
	.little_img .arrows {
		width: 30px;
		height: 74px;
		background: #F3F3F3;
		text-align: center;
		line-height: 74px;
		font-size: 24px;
		color: #C4C4C4;
	}
	
	.little_img .arrow_left {
		float: left;
		margin-right: 12px;
	}
	
	.little_img .arrow_right {
		float: right;
		margin-left: 12px;
	}
	
	.little_img_ul_trom {
		width: 415px;
		height: 74px;
		float: left;
		overflow: hidden;
	}
	
	.little_img_ul {
		width: 765px;
	}
	
	.little_img_li {
		float: left;
		width: 74px;
		height: 74px;
		margin-right: 11px;
		cursor: pointer;
	}
	
	.main_comm_con {
		width: 670px;
		float: left;
	}
	
	.main_comm_con .title {
		width: 100%;
		height: 64px;
		font-size: 22px;
		line-height: 32px;
		margin-bottom: 23px;
		color: #585858;
		font-weight: normal;
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
	}
	
	.main_comm_con .price {
		height: 80px;
		background: rgba(255, 232, 233, .5);
		line-height: 80px;
		color: #E62129;
		padding-left: 20px;
	}
	
	.main_comm_con .sp_left {
		color: #AFAFAF;
		margin-right: 22px;
		float: left;
	}
	
	.main_comm_con .price b {
		font-size: 20px;
		font-weight: normal;
		float: left;
	}
	
	.main_comm_con .salesNum {
		height: 54px;
		line-height: 54px;
		color: #585858;
		display: flex;
		padding-left: 20px;
	}
	
	.main_comm_con .salesNum label {
		font-size: 12px;
	}
	
	.main_comm_con .colour {
		padding-left: 20px;
		padding-bottom: 35px;
		border-bottom: 1px dashed #E5E5E5;
	}
	
	.main_comm_con .colour .colour_con {
		display: flex;
		float: left;
		width: 560px;
		flex-wrap: wrap;
		align-content: space-around;
	}
	
	.main_comm_con .colour .colour_con_item {
		padding: 9px 20px;
		border: 1px solid transparent;
		background: #F6F6F6;
		color: #AFAFAF;
		font-size: 14px;
		flex-grow: 0;
		border-radius: 2px;
		margin: 5px;
	}
	
	.main_comm_con .colour .item_active {
		background: #FFEEEE;
		border: 1px solid #E62129;
		color: #E62129;
	}
	
	.main_comm_con .commNum {
		padding: 26px 0 44px;
	}
	
	.main_comm_con .commNum_con {
		float: left;
		font-size: 16px;
		line-height: 34px;
		text-align: center;
	}
	
	.main_comm_con .commNum_con a {
		width: 32px;
		height: 32px;
		border: 1px solid #E62129;
		background: #FFDEDF;
		display: block;
		float: left;
		color: #E62129;
	}
	
	.main_comm_con .commNum_con .item_active {
		background: #E62129;
		color: #fff;
	}
	
	.main_comm_con .commNum_con .nums {
		float: left;
		width: 70px;
		color: #585858;
	}
	
	.main_comm_con .commNum_con .wenben {
		float: left;
		color: #585858;
		font-size: 14px;
		margin-left: 10px;
	}
	
	.main_comm_con .btn_con_left {
		float: left;
		width: 190px;
		height: 55px;
		text-align: center;
		line-height: 55px;
		border: 1px solid #E62129;
		background: #FFDEDF;
		color: #E62129;
		border-radius: 3px;
		margin-right: 18px;
		font-size: 16px;
		cursor: pointer;
	}
	.main_comm_con .btn_con_right {
		float: left;
		width: 257px;
		height: 55px;
		text-align: center;
		line-height: 55px;
		background: #E62129;
		color: #fff;
		border: none;
		font-size: 16px;
		border-radius: 3px;
		cursor: pointer;
	}
	.main_comm_con .btn_con_det{
		border: 1px solid #ccc;
		background: #eee;
		color: #585858;
	}
	.main_comm_con .btn_con_right .cart {
		width: 18px;
		height: 17px;
		vertical-align: sub;
		float: none;
	}
	
	.main_tab .el-tabs--card>.el-tabs__header {
		border: none;
	}
	
	.main_tab .el-tabs--card>.el-tabs__header .el-tabs__nav {
		border: none;
		height: 50px;
		background: #F6F6F6;
		width: 100%;
	}
	
	.main_tab .el-tabs__nav .el-tabs__item {
		border: none;
		height: 48px;
		line-height: 50px;
		border-top: 2px solid transparent;
		color: #AFAFAF;
	}
	
	.main_tab .el-tabs__nav .is-active {
		border-top: 2px solid #E62129;
		background: #fff;
	}
	
	.main_tab_pl {}
	
	.main_tab_pl h3 {
		color: #000;
		font-size: 18px;
		height: 44px;
		line-height: 44px;
	}
	
	.main_tab_pl .main_tab_pl_con {}
	
	.main_tab_pl .main_tab_pl_con_item {
		margin-top: 20px;
	}
	
	.main_tab_pl .main_tab_pl_con_item .item_pic {
		width: 38px;
		height: 38px;
		border-radius: 100%;
		float: left;
		margin-right: 18px;
	}
	
	.main_tab_pl .main_tab_pl_con_item .item_pic img {
		border-radius: 100%;
	}
	
	.main_tab_pl .main_tab_pl_con_item .item_ri_con {
		float: left;
		width: 1140px;
		border-bottom: 1px solid #E5E5E5;
	}
	
	.main_tab_pl .main_tab_pl_con_item .item_ri_con h4 {
		height: 38px;
		line-height: 38px;
		color: #AFAFAF;
		font-size: 14px;
	}
	
	.main_tab_pl .main_tab_pl_con_item .item_ri_con p {
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		font-size: 14px;
		color: #585858;
		line-height: 22px;
		margin-bottom: 10px;
	}
	
	.main_tab_pl .main_tab_pl_con_item .item_pic_in {
		display: flex;
		flex-wrap: wrap;
	}
	
	.main_tab_pl .main_tab_pl_con_item .item_pic_in img {
		width: 50px;
		height: 50px;
		margin-right: 20px;
	}
	
	.main_tab_pl .main_tab_pl_con_item .arrow_num {
		padding: 15px 0;
	}
	
	.main_tab_pl .main_tab_pl_con_item .arrow_num time {
		float: left;
		color: #AFAFAF;
	}
	
	.main_tab_pl .main_tab_pl_con_item .arrow_num .zand {
		float: right;
		color: #AFAFAF;
		line-height: 17px;
	}
	
	.main_tab_pl .main_tab_pl_con_item .arrow_num .zand img {
		float: left;
		margin-right: 9px;
		width: 17px;
		height: 17px;
	}
	
	.main_tab_pl .main_tab_pl_con_item .arrow_num .zan_active {
		color: #E62129;
	}
	
	.pages {
		padding: 80px 0;
	}
	
	.main_tab_pl .el-pagination {
		text-align: center;
	}
	
	.main_tab_pl .el-pagination.is-background .btn-next,
	.main_tab_pl .el-pagination.is-background .btn-prev,
	.main_tab_pl .el-pagination.is-background .el-pager li {
		border: 1px solid #B1B1B1;
		background: transparent;
	}
	
	.main_tab_pl .el-pagination.is-background .el-pager li:not(.disabled).active {
		border: 1px solid transparent;
		background: #E62129;
	}
	.main .main_bottom {
		height: 110px;
		line-height: 110px;
		text-align: center;
	}
	
	.main .main_bottom p {
		font-weight: normal;
		font-size: 16px;
		color: #AFAFAF;
		position: relative;
		display: inline-block;
	}
	
	.main .main_bottom p span {
		position: absolute;
		top: 55px;
		height: 1px;
		background: #E5E5E5;
		width: 95px;
	}
	
	.main .main_bottom p span:nth-child(1) {
		left: -110px;
	}
	
	.main .main_bottom p span:nth-child(2) {
		right: -110px;
	}
</style>


