		</div>
		<div class="header_nav">
			<ul class="width12 nav clearfix">
				<li :class="{li_active:path=='/'?true:false}">
					<a href="/">首页</a>
				</li>
				<li :class="{li_active:indexs==0?true:false}" @click="urlClick(0)">
					<a href="javascript:;">分类</a>
