				<li :class="{li_active:indexs==1?true:false}" @click="urlClick(1)">
					<a href="javascript:;">推荐</a>
				</li>
				<li :class="{li_active:indexs==2?true:false}" @click="urlClick(2)">
					<a href="javascript:;">最新</a>
				</li>
				<li :class="{li_active:indexs==3?true:false}" @click="urlClick(3)">
					<a href="javascript:;">热销</a>
				</li>
			</ul>
		</div>
		<div class="tc_model" v-if="centerDialogLogin || register ||forgetPaws"></div>
		<div class="login_box" v-if="centerDialogLogin">
			<div class="login_box_title">
				<span></span>
				<span></span>
				<h3>开始登陆</h3>
			</div>
			<div class="login_box_con">
				<p class="con_item">
					<span class="sp_img"><img src="../assets/images/user.png"/></span>
					<input type="text" class="inp_text" placeholder="请输入手机号" maxlength="11" v-model="form.username" @blur.prevent="changeCount()" />
				</p>
				<label class="requireds">{{formRed[0].username}}</label>
				<p class="con_item">
					<span class="sp_img"><img src="../assets/images/paw.png"/></span>
					<input type="password" class="inp_text" placeholder="请输入登录密码" v-model="form.password" name="password" @blur.prevent="changePaw()" />
				</p>
				<label class="requireds">{{formRed[1].paws}}</label>
				<input type="button" class="inp_but" @click="loginSbmit" value="登陆">
			</div>
			<div class="login_box_bot">
				<a href="javascript:;" :class="{a_active:centerDialogLogin}" @click="centerDialogLogin=true,register =false,forgetPaws=false">开始登录</a>
				<b>|</b>
				<a href="javascript:;" @click="centerDialogLogin=false,register =true,forgetPaws=false">注册账号</a>
				<b>|</b>
				<a href="javascript:;" @click="centerDialogLogin=false,register =false,forgetPaws=true">忘记密码</a>
			</div>
			<a href="javascript:;" class="login_box_close" @click="centerDialogLogin=false"><img src="../assets/images/login_close.png" /></a>
		</div>
		<div class="login_box" v-if="register">
			<div class="login_box_title">
				<span></span>
				<span></span>
				<h3>注册账号</h3>
			</div>
			<div class="login_box_con">
				<p class="con_item">
					<span class="sp_img"><img src="../assets/images/user.png"/></span>
					<input type="text" class="inp_text" maxlength="11" v-model="form.username" placeholder="请输入手机号" @blur.prevent="changeCount()" />
				</p>
				<label class="requireds">{{formRed[0].username}}</label>
				<p class="con_item">
					<span class="sp_img"><img src="../assets/images/yzm.png"/></span>
					<input type="text" class="inp_text" placeholder="输入验证码" />
					<button type="button" class="inp_buts">获取验证码</button>
				</p>
				<label class="requireds">{{formRed[1].username}}</label>
				<p class="con_item">
					<span class="sp_img"><img src="../assets/images/paw.png"/></span>
					<input type="password" class="inp_text" v-model="form.password" placeholder="设置登录密码" @blur.prevent="changePaw()" />
				</p>
				<label class="requireds">{{formRed[1].paws}}</label>
				<button type="button" @click="handleSubmit" class="inp_but">注册</button>
			</div>
			<div class="login_box_bot">
				<a href="javascript:;" @click="centerDialogLogin=true,register =false,forgetPaws=false">开始登录</a>
				<b>|</b>
				<a href="javascript:;" :class="{a_active:register}" @click="centerDialogLogin=false,register =true,forgetPaws=false">注册账号</a>
				<b>|</b>
				<a href="javascript:;" @click="centerDialogLogin=flase,register =false,forgetPaws=true">忘记密码</a>
			</div>
			<a href="javascript:;" class="login_box_close1" @click="register=false"><img src="../assets/images/login_close.png" /></a>
		</div>
		<div class="login_box" v-if="forgetPaws">
			<div class="login_box_title">
				<span></span>
				<span></span>
				<h3>忘记密码</h3>
			</div>
			<div class="login_box_con">
				<p class="con_item">
					<span class="sp_img"><img src="../assets/images/user.png"/></span>
					<input type="text" maxlength="11" class="inp_text" v-model="form.username" placeholder="请输入手机号" @blur.prevent="changeCount()" />
				</p>
				<p class="con_item">
					<span class="sp_img"><img src="../assets/images/yzm.png"/></span>
					<input type="text" class="inp_text" placeholder="输入验证码" />
					<button type="button" class="inp_buts">获取验证码</button>
				</p>
				<p class="con_item">
					<span class="sp_img"><img src="../assets/images/paw.png"/></span>
					<input type="password" class="inp_text" v-model="form.password" placeholder="设置登录密码" @blur.prevent="changePaw()" />
				</p>
				<button type="button" @click="forgetUser" class="inp_but">修改</button>
			</div>
			<div class="login_box_bot">
				<a href="javascript:;" @click="centerDialogLogin=true,register =false,forgetPaws=false">开始登录</a>
				<b>|</b>
				<a href="/registred" @click="centerDialogLogin=false,register =true,forgetPaws=false">注册账号</a>
				<b>|</b>
