			</div>
			<a href="javascript:;" class="login_box_close1" @click="forgetPaws=false"><img src="../assets/images/login_close.png" /></a>
		</div>
	</div>
</template>
<script>
	export default {
		name: 'Header',
		data() {
			return {
				id: this.$route.params.id,
				userInfo: {},
				userNames: '',
				form: {
					username: '',
					password: '',
					SmsCode: ''
				},
				formRed: [{
						username: ''
					},
					{
						paws: ''
					},
				],
				cartTotal: 0,
				inputs: '',
				logins: false,
				centerDialogLogin: false,
				register: false,
				forgetPaws: false,
				li_active: 'li_active',
				path: '',
				indexs: '',
			}
		},
		watch: {
			$route(to, from) {
				this.path = this.$route.path;
				if(this.$route.query.id == undefined) {
					this.indexs = 5
				} else {
					this.indexs = this.$route.query.id
				}
				if(sessionStorage.getItem('userInfo') != undefined && (sessionStorage.getItem('userInfo') != null || sessionStorage.getItem('userInfo') != '')) {
					this.logins = true;
					let userInfo = sessionStorage.getItem('userInfo')
					this.userInfo = JSON.parse(userInfo)
					//					console.log(this.userInfo)
					this.userNames = this.userInfo.userName != null ? this.userInfo.userName : this.userInfo.userLoginAcc
					//					console.log(this.userNames)
				} else {
					this.logins = false;
					this.userNames = '';
				}
			}
		},
		mounted() {
			this.path = this.$route.path
			if(this.$route.query.id == undefined) {
				this.indexs = 5
			} else {
				this.indexs = this.$route.query.id
			}
			if(sessionStorage.getItem('userInfo') != undefined && (sessionStorage.getItem('userInfo') != null || sessionStorage.getItem('userInfo') != '')) {
				this.logins = true;
				let userInfo = sessionStorage.getItem('userInfo')
				this.userInfo = JSON.parse(userInfo)
				//				console.log(this.userInfo)
				this.userNames = this.userInfo.userName != null ? this.userInfo.userName : this.userInfo.userLoginAcc
				//				console.log(this.userNames)
				this.cartNum()
			} else {
				this.userNames = ''
			}
		},
		created() {
			this.$root.$on("cartNum", (item) => {
				console.log(item)
				this.cartNum()
			})
			this.$root.$on("login", (item) => {
				console.log(item)
				this.centerDialogLogin = true
			})
		},
		methods: {
			cartNum() {
				const _this = this
				let data = {
					'': _this.userInfo.id
				}
				_this.ajax.cartNums(data).then(res => {
					const rs = res.data;
					if(rs.code == 200) {
						_this.cartTotal = rs.data
					} else {
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error);
				});
			},
			urlClick(val) {
				this.$router.push({
					path: '/classification/',
					query: {
						id: val
					}
				})
			},
			keyClick() { //关键字搜索
				const _this = this
				if(_this.inputs != '') {
					_this.$router.push({
						path: '/classification/',
						query: {
							id: 0,
							keys: _this.inputs
						}
					})
				}
			},
			changeCount() { //账号
				let _this = this
				var reg = 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
				if(_this.form.username == '') {
					_this.formRed[0].username = '请输入手机号码';
				} else if(!reg.test(_this.form.username)) {
					_this.formRed[0].username = '请输入正确的手机号';
				} else {
					_this.formRed[0].username = '';
				}
			},
			changePaw() {
				let _this = this
				var resg = /^[0-9A-Za-z]{6,20}$/
				if(_this.form.password == '') {
					_this.formRed[0].username = '请输入密码';
				} else if(!resg.test(_this.form.password)) {
					_this.formRed[0].username = '只能是数字和英文字母';
				} else if(_this.form.password.length < 6) {
					_this.formRed[0].username = '请输入不能小于6位';
				}
			},
			reomves() { //注销
				sessionStorage.removeItem("userInfo")
				this.logins = false
				window.location.reload()
			},
			loginSbmit() { //登录
				let _that = this
				var data = {
					UserLoginAcc: _that.form.username,
					UserLoginPwd: _that.form.password,
				}
				_that.ajax.login(data).then(res => {
					const rs = res.data;
					if(rs.code == 200) {
						_that.centerDialogLogin = false
						_that.logins = true
						_that.showMsg(rs.msg);
						console.log(rs.data.userInfoDto)
						let indst = JSON.stringify(rs.data.userInfoDto)
						sessionStorage.setItem('userInfo', indst)
						this.userNames = rs.data.userInfoDto.userName == '' || rs.data.userInfoDto.userName == null ? rs.data.userInfoDto.userLoginAcc : rs.data.userInfoDto.userName
					} else {
						_that.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error);
				});
			},
			handleSubmit() { //注册
				let _that = this
				var data = {
					UserLoginAcc: _that.form.username,
					UserLoginPwd: _that.form.password,
					SmsCode: 123456
				}
				_that.ajax.insertUser(data).then(res => {
					const rs = res.data;
					if(rs.code == 200) {
						_that.register = false
						//_that.centerDialogLogin = true
						_that.showMsg('注册成功');
					} else {
						_that.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error);
				});
			},
			forgetUser() {
				let _that = this
				var data = {
					UserLoginAcc: _that.form.username,
					UserLoginPwd: _that.form.password,
					SmsCode: 123456
				}
				_that.ajax.forgetUser(data).then(res => {
					const rs = res.data;
					if(rs.code == 200) {
						_that.forgetPaws = false
						sessionStorage.removeItem("userInfo")
						_that.logins = false
						_that.centerDialogLogin = true
						_that.showMsg(rs.msg);
					} else {
						_that.showMsg(rs.msg, '2');
					}
				}).catch(function(error) {
					console.log(error);
				});
			},
			hrefUrl() {
				const _this = this
				if(sessionStorage.getItem('userInfo') != undefined && (sessionStorage.getItem('userInfo') != null || sessionStorage.getItem('userInfo') != '')) {
					_this.$router.push({
						name: 'cart',
					})
				}else{
					_this.showMsg('您还没有登录哦！快去登录吧！！！', '2');
				}
			},
			hrefUrl1(val) {
				const _this = this
				if(sessionStorage.getItem('userInfo') != undefined && (sessionStorage.getItem('userInfo') != null || sessionStorage.getItem('userInfo') != '')) {
					if(val == 1) {
						_this.$router.push({
							path: '/editData',
						})
					} else if(val == 2) {
						_this.$router.push({
							path: '/Order',
						})
					} else if(val == 3) {
						_this.$router.push({
							path: '/receiptCity',
						})
					}
				}else{
					_this.showMsg('您还没有登录哦！快去登录吧！！！', '2');
				}
			}
		},
	}
</script>

