<template>
	<div class="banner">
		<el-carousel trigger="click" height="400px">
			<el-carousel-item v-for="(item,index) in bannerList" :key="index">
				<img :src="apiUrl+item.bannerUrl"/>
			</el-carousel-item>
		</el-carousel>
	</div>
</template>

<script>
	export default {
		name: 'banner',
		data() {
			return {
				bannerList:[],
			}
		},
		mounted(){
			this.info()
		},
		methods:{
			info(){
				const _this = this
				let data = {}
				_this.ajax.homeBanner(data).then(res => {
					const rs = res.data
					if(rs.code == 200){
						_this.bannerList = rs.data
					}else{
						_this.showMsg(rs.msg, '2');
					}
				}).catch(function(error){
					console.log(error)
				})
			}
		},
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
	.banner .el-carousel__item h3 {
		color: #475669;
		font-size: 14px;
		opacity: 0.75;
		line-height: 150px;
		margin: 0;
	}
	/*.banner .el-carousel__item{
		background-color: #000;
	}
	.banner .el-carousel__item:nth-child(2n) {
		background-color: #000;
	}
	.banner .el-carousel__item:nth-child(2n+1) {
		background-color: #000;
	}*/
	.banner .el-carousel__indicators{
		bottom: 30px;
	}
	.banner .el-carousel__indicators .el-carousel__indicator{
		width: 10px;
		height: 10px;
		border-radius: 100%;
		background:rgba(255,255,255,.5);
		padding: 0;
		margin-right: 10px;
	}
	.banner .el-carousel__button{
		width: 10px;
		height: 10px;
		border-radius: 100%;
	}
	.banner .el-carousel__indicators .is-active{
		background: #fff;
	}
</style>


