import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/home/index'
import News from '@/views/home/news'
import classification from '@/views/classification/index'
import commDetails from '@/views/commDetails/index'
import cart from '@/views/cart/index'
import verify from '@/views/cart/verify'
import pay from '@/views/cart/pay'

import personal from '@/views/personal/index'
import editData from '@/views/personal/editData'
import receiptCity from '@/views/personal/receiptCity'
import Order from '@/views/personal/order'
import orderDetails from '@/views/personal/orderDetails'
import appraise from '@/views/personal/appraise'


Vue.use(Router)

export default new Router({
	//mode: 'history',
	routes: [{
			//首页
			path: '/',
			name: 'Home',
			component: Home,
		},
		{
			//首页
			path: '/home',
			name: 'Home',
			component: Home,
		},
		{
			path: '/classification',
			name: 'classification',
			component: classification
			//分类
		},
		{
			path: '/commDetails/:id',
			name: 'commDetails',
			component: commDetails
			//商品详情
		},
		{
			path: '/cart',
			name: 'cart',
			component: cart
			//购物车
		},
		{
			//个人中心
			path: '/personal',
			name: 'personal',
			component: personal,
			children:[
				{
					//编辑资料
					path: '/editData',
					name: 'editData',
					component: editData
				},
				{
					//收货地址
					path: '/receiptCity',
					name: 'receiptCity',
					component: receiptCity
				},
				{
					//我的订单
					path: '/order',
					name: 'Order',
					component: Order
				},
			]
		},
		{
			//选择收货地址页面
			path: '/verify/:carids',
			name: 'verify',
			component: verify
		},
		{
			//选择支付方式
			path: '/pay',
			name: 'pay',
			component: pay
		},
		{
			//新闻详情
			path: '/news',
			name: 'News',
			component: News
		},
		{
			//订单详情
			path: '/orderDetails',
			name: 'orderDetails',
			component: orderDetails
		},
		{
			//订单评价
			path: '/appraise',
			name: 'appraise',
			component: appraise
		},
	]
})


